// post请求
// get请求
function getIncomeListData () { // 收益明细页面
  return Axios({
    url: ul.local + '/icomeList.json',
    method: 'get'
  })
}
function getData (url) {
  return Axios({
    url: ul.local + url,
    method: 'get'
  })
}
function weixin (data) {
  return Axios({
    url: 'WechatJsSdk/getSignPackage',
    method: 'post',
    data: data
  })
}
function login (data) { // 登录
  return Axios({
    url: 'ShareLogin/mlogin',
    method: 'post',
    data: data
  })
}
function isLogin () { // 判断是否已登录
  return Axios({
    url: 'ShareLogin/member_is_login',
    method: 'post'
  })
}
function getUserInfo () { // 获取会员个人信息
  return Axios({
    url: 'User/get_user_info_interface',
    method: 'get'
  })
}
function getMyReferrer (data) { // 获取我的推荐人
  return Axios({
    url: 'ShareLogin/get_share_referrer',
    method: 'post',
    data: data
  })
}
function checkIdauthStatus () {
  return Axios({
    url: 'User/check_idauth_status',
    method: 'post'
  })
}
export {getIncomeListData, getData, weixin, login, isLogin, getUserInfo, getMyReferrer, checkIdauthStatus}



