<template>
  <article class="subordinate-member">
    <back-head>
      <span class="back-content">下级会员</span>
      <span class="mui-icon mui-icon-search"></span>
    </back-head>
    <section class="subordinate-member-tab">
      <div v-for="item in tabList" :key="item.type" :class="{'tab-active':item.isActive}" @click="getData(item.type)">
        <p v-text="item.content">类型</p>
        <span v-text="'('+item.num+')'"></span>
      </div>
    </section>
    <section class="subordinate-member-content">
      <section class="subordinate-member-content-list" v-for="item in withdrawRecordList" :key="item.id">
        <img src="../../../assets/images/icon/tubiao_108.png" alt="" v-if="item.status==='success'">
        <img src="../../../assets/images/icon/tubiao_109.png" alt="" v-else>
        <div>
          <div class="subordinate-member-content-list-info">
            <p v-text="item.member">2392839382</p>
            <span v-text="item.name">王*君</span>
          </div>
          <div class="subordinate-member-content-list-status">
            <p :class="'withdraw-'+item.status" v-text="item.statusContent">3,000.00</p>
            <span v-text="item.data">2018-08-27 17:02</span>
          </div>
        </div>
      </section>
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  components: {
    BackHead
  },
  data () {
    return {
      withdrawStatus: 'processed',
      withdrawRecordList: [],
      tabList: [
        {
          type: 'all',
          isActive: true,
          content: '全部',
          num: 30
        },
        {
          type: 'succed',
          isActive: false,
          content: '已开通',
          num: 20
        },
        {
          type: 'failed',
          isActive: false,
          content: '未开通',
          num: 10
        }
      ]
    }
  },
  methods: {
    getData (msg) {
      this.tabList.forEach((item) => {
        if (item.type === msg) {
          item.isActive = true
        } else {
          item.isActive = false
        }
      })
      this.$api.getData('/subordinateMemberList.json').then(res => {
        this.withdrawRecordList = res.data[msg]
      })
    }
  },
  created () {
    this.getData('all')
  }
}
</script>
<style lang="scss" scoped>
@import '~styles/common.scss';
.subordinate-member-tab {
  display: flex;
  flex-direction: row;
  height: 0.79rem;
  box-sizing: border-box;
  border-bottom: 1px solid #d7d7d7;
  background-color: #ffffff;
  div{
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
}
.subordinate-member-content {
  background-color: #fff;
  section:last-child{
    &>div{
      border-bottom: none;
    }
  }
  .subordinate-member-content-list {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 1.51rem;
    img {
      width: 0.97rem;
      height: 0.97rem;
      margin-left: 0.35rem;
      margin-right: 0.2rem;
    }
    & > div{
      flex:1;
      display: flex;
      flex-direction: row;
      align-items: center;
      height: 100%;
      border-bottom: 1px solid #e6e6e6;
      .subordinate-member-content-list-info {
        span {
          display: inline-block;
          font-size: 0.24rem;
          color: #999;
          width: 100%;
          text-align: right;
        }
      }
      .subordinate-member-content-list-status {
        flex: 1;
        text-align: right;
        padding-right: 0.36rem;
        span{
          color: #999;
        }
      }
    }
  }
}
.tab-active{
  font-weight: 600;
  position: relative;
  &::after{
    content: '';
    display: inline-block;
    height: 2px;
    width: 1.56rem;
    background-color: #b20dba;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%)
  }
}
.withdraw-success {
  color: #4bb0f9;
}
.withdraw-failed {
  color: #3ad479;
}
</style>



