<template>
  <article class="phone-verification-code">
    <back-head>
      <span class="back-content">验证码</span>
    </back-head>
    <section class="phone-verification-code-content">
      <div class="tips">
        <p>我们已发送验证码到您的手机</p>
        <span>123******134</span>
      </div>
      <div class="phone-verification-code-content-code-wrapper" ref="codeWrapper">
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)'/>
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)'/>
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)'/>
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)'/>
      </div>
      <!-- <router-link :to="{name:'IdentificationCardVerification'}" class="mui-btn next">下一步</router-link> -->
      <router-link class="phone-verification-code-content-change-way" :to="{name:'DontRember'}">换个方式重置密码</router-link>
    </section>
    <footer>
      <foot-next :is-active="canNext" :component-name="'IdentificationCardVerification'"></foot-next>
    </footer>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
import FootNext from 'commonComponent/footNext/FootNext'

export default {
  components: {
    BackHead,
    FootNext
  },
  data () {
    return {
      verificationCode: [],
      canNext: false
    }
  },
  methods: {
    watchInp (event) {
      var e = event || window.event
      var target = e.target || e.srcElement
      var inps = this.$refs.codeWrapper.children
      var isNumber = /[\d]/.test(target.value)
      if (isNumber) {
        for (var i = 0; i < inps.length; i++) {
          if (target === inps[i]) {
            if (this.verificationCode['num' + i] === 'done') {
              target.value = target.value[1]
            }
            this.$set(this.verificationCode, i, target.value)
            this.verificationCode['num' + i] = 'done'
            if (i < inps.length - 1 && this.verificationCode['num' + (i + 1)] !== 'done') { // 当下一个输入框没有有效值时，获取焦点
              inps[i + 1].focus()
              this.verificationCode['num' + (i + 1)] = 'will'
            }
          }
        }
      } else {
        target.value = ''
      }
    },
    deleteCode (event) {
      var e = event || window.event
      var target = e.target || e.srcElement
      var inps = this.$refs.codeWrapper.children
      for (var i = 0; i < inps.length; i++) {
        if (target === inps[i] && i >= 0) {
          // ['num'+i] 属性有四个值，表示当前输入的四个状态：
          // will：刚获得焦点，未输入值
          // empty: 获得焦点后，输入了值，然后被删除了值
          // clear: 失去焦点并且没有输入值
          // done: 输入框有有效值
          // 第一次点击删除键，删除内容，并给对应的['num'+i]赋值为empty
          // empty 表示原先已有内容，现在删除了，
          // 第二次点击删除键，则退回上一个输入框，并把['num'+i]赋值为'claer'
          // 当焦点自动跳转到当前输入框，此时输入框没有内容，点击删除键(第一次)
          this.$set(this.verificationCode, i, target.value)
          if (this.verificationCode['num' + i] === 'empty' || this.verificationCode['num' + i] === 'will') {
            if (i !== 0) {
              inps[i - 1].focus()
            }
            this.verificationCode['num' + i] = 'cealr'
          } else {
            this.verificationCode['num' + i] = 'empty'
          }
        }
      }
    }
  },
  // computed: {
  //   isNext: function () {
  //     console.log(this.verificationCode.length)
  //     return this.verificationCode
  //   }
  // }
  watch: {
    verificationCode: function (val) {
      if (val.join('').length === 4) {
        this.canNext = true
      } else {
        this.canNext = false
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~styles/common.scss';
.phone-verification-code{
  display:flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
  background-color: #f7f7f7;
}
.phone-verification-code-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  .tips{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #000;
    margin-top: 0.7rem;
    margin-bottom: 0.45rem;
    p{
      color: #000;
    }
  }
  .phone-verification-code-content-code-wrapper {
    width: 100%;
    padding: 0;
    display: flex;
    justify-content: center;
    margin-bottom: 0.45rem;
    & > input {
      box-sizing: border-box;
      width: 0.74rem;
      height: 0.74rem;
      border-radius: 0;
      text-align: center;
      border: 1px solid #b3b3b3;
      border-right: none;
      &:last-child{
        border-right: 1px solid #b3b3b3;
      }
    }
  }
  .phone-verification-code-content-change-way {
    align-self: center;
    color: #169dff;
  }
}
</style>



