<template>
  <article class="withdraw-record">
    <back-head>
      <span class="back-content">提现记录</span>
    </back-head>
    <section class="withdraw-record-tab">
      <p v-for="item in tabList" :key="item.type" v-text="item.content" @click="getData(item.type)" :class="{'tab-active':item.isActive}"></p>
    </section>
    <section class="withdraw-record-content">
      <div class="withdraw-record-content-list" v-for="item in withdrawRecordList" :key="item.id" @click="goDetailPage(item)">
        <img src="#" alt="">
        <div class="content">
          <div class="withdraw-record-content-list-info">
            <p v-text="item.account">体现到云付通余额</p>
            <span v-text="item.data">08-30 14:52</span>
          </div>
          <div class="withdraw-record-content-list-status">
            <p v-text="item.amount">3,000.00</p>
            <span :class="'withdraw-'+item.status" v-text="item.statusContent">处理中</span>
          </div>
        </div>
      </div>
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  components: {
    BackHead
  },
  data () {
    return {
      withdrawStatus: 'processed',
      withdrawRecordList: [],
      tabList: [
        {
          type: 'all',
          isActive: true,
          content: '全部'
        }, {
          type: 'succed',
          isActive: false,
          content: '提现成功'
        }, {
          type: 'processed',
          isActive: false,
          content: '处理中'
        }
      ]
    }
  },
  methods: {
    getData (msg) {
      this.tabList.forEach((item) => {
        if (item.type === msg) {
          item.isActive = true
        } else {
          item.isActive = false
        }
      })
      this.$api.getData('/withdrawRecord.json').then(res => {
        this.withdrawRecordList = res.data[msg]
      })
    },
    goDetailPage (item) {
      this.$router.push({name: 'WithdrawRecordDetail', params: item})
    }
  },
  created () {
    this.getData('all')
  }
}
</script>
<style lang="scss" scoped>
@import '~styles/common.scss';
.withdraw-record-tab {
  display: flex;
  flex-direction: row;
  height: 0.79rem;
  box-sizing: border-box;
  border-bottom: 1px solid #d7d7d7;
  p{
    flex: 1;
    text-align: center;
    line-height: 0.79rem;
  }
}
.withdraw-record-content {
  .withdraw-record-content-list {
    display: flex;
    flex-direction: row;
    height: 1.5rem;
    align-items: center;
    background-color: #fff;
    img{
      width: 0.96rem;
      height: 0.96rem;
      border: 1px solid #ccc;
      border-radius: 50%;
      margin-left: 0.38rem;
    }
    .content{
      flex: 1;
      display: flex;
      flex-direction: row;
      border-bottom: 1px solid #d7d7d7;
      margin-left: 0.25rem;
      height: 100%;
      align-items: center;
      div{
        flex: 1;
        text-align: right;
        span{
          font-size: 0.24rem;
        }
      }
      .withdraw-record-content-list-info{
        &>span{
          color: #9e9e9e;
        }
      }
      .withdraw-record-content-list-status{
        flex: 1;
        text-align: right;
        padding-right: 0.39rem;
        p{
          font-weight: 600;
        }
      }
    }
  }
  .withdraw-record-content-list:last-child{
    .content{
      border: none;
    }
  }
}
.tab-active{
  font-weight: 600;
  position: relative;
  &::after{
    content: '';
    display: inline-block;
    height: 2px;
    width: 1.56rem;
    background-color: #b20dba;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%)
  }
}
.withdraw-processed{
  color: #33d27d;
}
.withdraw-success{
  color: #5ab1f4;
}
.withdraw-failed{
  color: #fc132b;
}
</style>



