import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login/Login'
import EntryMy from '@/components/EntryMy'
import SharingSystem from '@/components/sharingSystem/SharingSystem'
// 已开通
// import ShareOpened from '@/components/sharingSystem/opened/ShareOpened'
import EarningsExplain from '@/components/sharingSystem/EarningsExplain'
import SubordinateMember from '@/components/sharingSystem/opened/SubordinateMember'
import MessageList from '@/components/sharingSystem/opened/MessageList'
// 已开通 --> 收益提现
import WithdrawWrapper from '@/components/sharingSystem/opened/withdraw/WithdrawWrapper'
import Withdraw from '@/components/sharingSystem/opened/withdraw/Withdraw'
// 已开通 --> 收益提现 --> 体现密码
import WithdrawPasswordWrapper from '@/components/sharingSystem/opened/withdraw/withdrawPassword/WithdrawPasswordWrapper'
import WithdrawPassword from '@/components/sharingSystem/opened/withdraw/withdrawPassword/WithdrawPassword'
import Rember from '@/components/sharingSystem/opened/withdraw/withdrawPassword/Rember'
import DontRember from '@/components/sharingSystem/opened/withdraw/withdrawPassword/DontRember'
import PhoneVerificationCode from '@/components/sharingSystem/opened/withdraw/withdrawPassword/PhoneVerificationCode'
import IdentificationCardVerification from '@/components/sharingSystem/opened/withdraw/withdrawPassword/IdentificationCardVerification'
import SetPassword from '@/components/sharingSystem/opened/withdraw/withdrawPassword/SetPassword'
import SetPasswordConfirm from '@/components/sharingSystem/opened/withdraw/withdrawPassword/SetPasswordConfirm'
// 已开通 --> 收益提现 --> 提现记录
import WithdrawRecordWrapper from '@/components/sharingSystem/opened/withdraw/withdrawRecord/WithdrawRecordWrapper'
import WithdrawRecord from '@/components/sharingSystem/opened/withdraw/withdrawRecord/WithdrawRecord'
import WithdrawRecordDetail from '@/components/sharingSystem/opened/withdraw/withdrawRecord/WithdrawRecordDetail'
// 已开通 --> 收益明细
import IncomeWrapper from '@/components/sharingSystem/opened/incomeDetails/IncomeWrapper'
import IncomeDetail from '@/components/sharingSystem/opened/incomeDetails/IncomeDetail'
import IncomeList from '@/components/sharingSystem/opened/incomeDetails/IncomeList'
import IncomSearch from '@/components/sharingSystem/opened/incomeDetails/incomeSearch'
// 已开通 --> 我的资料(已开通)
import UserInfoWrapper from '@/components/sharingSystem/opened/userInfo/UserInfoWrapper'
import MyProfile from '@/components/sharingSystem/opened/userInfo/MyProfile'
import PersonData from '@/components/sharingSystem/opened/userInfo/PersonData'
import MyReferences from '@/components/sharingSystem/opened/userInfo/MyReferences'
// 已开通 --> 我的资料 --> 实名认证
import RealNameAuthenticationWrapper from '@/components/sharingSystem/opened/userInfo/realNameAuthentication/RealNameAuthenticationWrapper'
import RealNameAuthentication from '@/components/sharingSystem/opened/userInfo/realNameAuthentication/RealNameAuthentication'
import PersonInformation from '@/components/sharingSystem/opened/userInfo/realNameAuthentication/PersonInformation'
// 未开通
// 未开通 --> 票务升级会员（未实名）
import UpgradeMembershipWrapper from '@/components/sharingSystem/notOpened/upgradeMembership/UpgradeMembershipWrapper'
import UpgradeMembershipNRN from '@/components/sharingSystem/notOpened/upgradeMembership/UpgradeMembershipNRN'
import VerificationSuccess from '@/components/sharingSystem/notOpened/upgradeMembership/VerificationSuccess'
// 未开通 --> 票务升级会员 --> 实名认证
import RealNameWrite from '@/components/sharingSystem/notOpened/upgradeMembership/RealNameWrite'
// 未开通 --> 票务升级会员（已实名）
import UpgradeMembershipRN from '@/components/sharingSystem/notOpened/UpgradeMembershipRN'
// 未开通 --> 我的资料(未开通)
import MyProfileWrapp from '@/components/sharingSystem/notOpened/MyProfile/MyProfileWrapper'
import MyProfileNotActive from '@/components/sharingSystem/notOpened/MyProfile/MyProfileNotActive'
import PersonDataNotActive from '@/components/sharingSystem/notOpened/MyProfile/PersonDataNotActive'
import MyReferencesNotActive from '@/components/sharingSystem/notOpened/MyProfile/MyReferencesNotActive'
// 未开通 --> (支付验证,验证结果)
import VerifyThePayment from '@/components/sharingSystem/notOpened/VerifyThePayment'
import PaymentVerificationResult from '@/components/sharingSystem/notOpened/PaymentVerificationResult'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'EntryMy',
      component: EntryMy
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/sharingSystem', // 分享系统主页面
      component: SharingSystem,
      meta: {
        auth: true
      },
      children: [
        {
          path: 'earningsExplain',
          name: 'EarningsExplain',
          component: EarningsExplain
        }, {//  消息
          path: 'messageList',
          name: 'MessageList',
          component: MessageList
        }, {// 下级会员
          path: 'subordinateMember',
          name: 'SubordinateMember',
          component: SubordinateMember
        }, {// 收益提现
          path: 'withdraw', // 提现主页面
          component: WithdrawWrapper,
          children: [
            {
              path: '/',
              name: 'Withdraw',
              component: Withdraw
            }, {
              path: 'withdrawPassword', // 提现密码主页面
              component: WithdrawPasswordWrapper,
              children: [
                {
                  path: '/',
                  name: 'WithdrawPassword',
                  component: WithdrawPassword
                }, {
                  path: 'rember',
                  name: 'Rember',
                  component: Rember
                }, {
                  path: 'forget',
                  name: 'DontRember',
                  component: DontRember
                }, {
                  path: 'phoneVerificationCode',
                  name: 'PhoneVerificationCode',
                  component: PhoneVerificationCode
                }, {
                  path: 'identificationCardVerification',
                  name: 'IdentificationCardVerification',
                  component: IdentificationCardVerification
                }, {
                  path: 'setPassword',
                  name: 'SetPassword',
                  component: SetPassword
                }, {
                  path: 'setPasswordConfirm',
                  name: 'SetPasswordConfirm',
                  component: SetPasswordConfirm
                }
              ]
            }, {
              path: 'withdrawRecord',
              component: WithdrawRecordWrapper,
              children: [
                {
                  path: '',
                  name: 'WithdrawRecord',
                  component: WithdrawRecord
                }, {
                  path: 'withdrawRecordDetail',
                  name: 'WithdrawRecordDetail',
                  component: WithdrawRecordDetail
                }
              ]
            }
          ]
        }, {// 收益明细
          path: 'income', // 收益明细主页面
          component: IncomeWrapper,
          children: [
            {
              path: '/',
              name: 'IncomeList',
              component: IncomeList
            }, {
              path: 'incomeDetail',
              name: 'IncomeDetail',
              component: IncomeDetail
            }, {
              path: 'incomeSearch',
              name: 'IncomeSearch',
              component: IncomSearch
            }
          ]
        }, {// 我的资料
          path: 'user',
          component: UserInfoWrapper,
          children: [
            {
              path: '',
              name: 'MyProfile',
              component: MyProfile
            }, {
              path: 'personData',
              name: 'PersonData',
              component: PersonData
            }, {
              path: 'myReferences',
              name: 'MyReferences',
              component: MyReferences
            }, {
              path: 'realNameAuthentication',
              component: RealNameAuthenticationWrapper,
              children: [
                {
                  path: '',
                  name: 'RealNameAuthentication',
                  component: RealNameAuthentication
                }, {
                  path: 'personInformation',
                  name: 'PersonInformation',
                  component: PersonInformation
                }
              ]
            }
          ]
        },
        { // 未开通 --> 已实名
          path: 'upgradeMembershipRN',
          name: 'UpgradeMembershipRN',
          component: UpgradeMembershipRN
        }, { // 未开通 --> 未实名
          path: 'upgradeMembershipNRN',
          component: UpgradeMembershipWrapper,
          children: [
            {
              path: '',
              name: 'UpgradeMembershipNRN',
              component: UpgradeMembershipNRN
            }, { // 未开通 --> 未实名 --> 实名认证填写
              path: 'realNameWrite',
              name: 'RealNameWrite',
              component: RealNameWrite
            }, {
              path: 'varificationSuccess',
              name: 'VerificationSuccess',
              component: VerificationSuccess
            }
          ]
        }, { // 未开通 --> 我的资料
          path: 'myProfileNotActive',
          component: MyProfileWrapp,
          children: [
            {
              path: '',
              name: 'MyProfileNotActive',
              component: MyProfileNotActive
            }, {
              path: 'personDataNotActive',
              name: 'PersonDataNotActive',
              component: PersonDataNotActive
            }, {
              path: 'myReferencesNotActive',
              name: 'MyReferencesNotActive',
              component: MyReferencesNotActive
            }
          ]
        }, { // 为开通 --> 支付认证
          path: 'verifyThePayment',
          name: 'VerifyThePayment',
          component: VerifyThePayment
        }, { // 未开通 --> 支付验证结果
          path: 'paymentVerificationResult',
          name: 'PaymentVerificationResult',
          component: PaymentVerificationResult
        }
      ]
    }
  ]
})



