<template>
  <div class="sharing-system">
    <div :is="showWitch" v-show="isShow"></div>
    <router-view></router-view>
  </div>
</template>
<script>
import ShareOpened from '@/components/sharingSystem/opened/ShareOpened'
import ShareNotOpened from '@/components/sharingSystem/notOpened/ShareNotOpened'
import {mapState} from 'vuex'
export default {
  components: {
    ShareOpened, ShareNotOpened
  },
  data () {
    return {
      isShow: true,
      showWitch: ''
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.showWitch = vm.isMember ? 'ShareOpened' : 'ShareNotOpened'
      if (to.path === '/sharingSystem') {
        vm.isShow = true
      } else {
        vm.isShow = false
      }
    })
    next()
  },
  beforeRouteUpdate (to, from, next) {
    if (from.path === '/sharingSystem') {
      this.isShow = false
    } else if (to.path === '/sharingSystem') {
      this.isShow = true
    }
    next()
  },
  computed: mapState(['isMember']),
  created () {
    this.showWitch = this.isMember ? 'ShareOpened' : 'ShareNotOpened'
  }
}
</script>
<style>
  .sharing-system{
    height: 100%;
  }
</style>



