<template>
  <article class="keyboard" v-show="show">
    <section class="keyboard-content">
      <div class="keyboard-top">
        <div class="top-title">
          <span class="icon-left" @click="close"></span>
          <p>请输入支付密码</p>
        </div>
        <div class="top-balance">
          <span>票务余额:</span>
          <span v-text="balance">0.00</span>
        </div>
      </div>
      <div class="input-wrapper">
        <span v-text="passwordArr[0]"></span>
        <span v-text="passwordArr[1]"></span>
        <span v-text="passwordArr[2]"></span>
        <span v-text="passwordArr[3]"></span>
        <span v-text="passwordArr[4]"></span>
        <span v-text="passwordArr[5]"></span>
      </div>
      <div class="keys" ref="key">
        <div class="key" v-for="(item,ind) in keyboardNum" :key="ind" :class="item.className?item.className:''" @click="keyDown($event,item,ind)">
          <span v-text="item.num"></span>
          <span v-text="item.letters"></span>
        </div>
      </div>
    </section>
  </article>
</template>
<script>
export default {
  props: ['isShow'],
  data () {
    return {
      passwordArr: [],
      // show: this.isShow,
      balance: 200.0,
      keyboardNum: [
        {
          num: 1,
          letters: ''
        },
        {
          num: 2,
          letters: 'ABC'
        },
        {
          num: 3,
          letters: 'DEF'
        },
        {
          num: 4,
          letters: 'GHI'
        },
        {
          num: 5,
          letters: 'JKL'
        },
        {
          num: 6,
          letters: 'MNO'
        },
        {
          num: 7,
          letters: 'PQRS'
        },
        {
          num: 8,
          letters: 'TUV'
        },
        {
          num: 9,
          letters: 'WXYZ'
        },
        {
          num: '',
          letters: '',
          className: 'empty'
        },
        {
          num: 0,
          letters: ''
        },
        {
          num: '',
          letters: '',
          className: 'del'
        }
      ]
    }
  },
  computed: {
    show: function () {
      return this.isShow
    }
  },
  methods: {
    keyDown (event, item, ind) {
      var passwordLength = this.passwordArr.length
      if ((item.num || item.num === 0) && passwordLength < 6) {
        this.passwordArr.push(item.num)
        if (passwordLength >= 5) {
          // 是否设置了支付密码，如果设置了，发送验证请求，没有则弹出提示框
          // 如果密码到了6位，发送验证请求
          // 验证成功后：跳转到验证成功界面
          this.$emit('changeShowState', false)
          this.$router.push({name: 'PaymentVerificationResult'})
        }
        this.keyboardNum[ind].className = 'active'
        setTimeout(() => {
          this.$set(this.keyboardNum, ind, {num: item.num, letters: item.letters})
        }, 100)
      }
      if (item.className === 'del' && passwordLength !== 0) {
        this.passwordArr.pop()
      }
    },
    close () {
      this.$emit('changeShowState', false)
    }
  }
}
</script>
<style>
.keyboard {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.7);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.keyboard .keyboard-content {
  display: flex;
  flex-direction: column;
  height: 7.58rem;
  background-color: #f7f7f7;
}
.keyboard .keyboard-content .keyboard-top {
  display: flex;
  flex-direction: column;
}
.keyboard .keyboard-content .keyboard-top div:nth-of-type(1) {
  height: 1.09rem;
  line-height: 1.09rem;
  font-weight: 600;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-bottom: 1px solid #ececed;
}
.keyboard .keyboard-content .keyboard-top div:nth-of-type(1) span {
  display: inline-block;
  width: 0.19rem;
  height: 0.34rem;
  background-image: url('data:image/png;base64,');
  background-size: cover;
  background-repeat: no-repeat;
  margin-right: 1.9rem;
  margin-left: 0.38rem;
}
.keyboard .keyboard-content .keyboard-top div:nth-of-type(2) {
  padding-top: 0.32rem;
  padding-bottom: 0.3rem;
  padding-left: 0.28rem;
}
.keyboard .keyboard-content .keyboard-top div:nth-of-type(2) span {
  color: #393939;
  font-size: 0.32rem;
}
.keyboard .keyboard-content .input-wrapper {
  flex: 1;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.keyboard .keyboard-content .input-wrapper span{
  display: inline-block;
  width: 1.16rem;
  height: 1.11rem;
  border: 1px solid #cdcdcd;
  border-right: none;
  line-height: 1.11rem;
  text-align: center;
}
.keyboard .keyboard-content .input-wrapper span:last-of-type {
  border-right: 1px solid #cdcdcd;
}
.keyboard .keyboard-content .keys {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}
.keyboard .keyboard-content .keys .key{
  flex:1 1 30%;
  height: 1.01rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #c6c6c6;
  border-right: 1px solid #c6c6c6;
  font-size: 0.24rem;
  line-height: 0.36rem;
}
.keyboard .keyboard-content .keys .key:nth-child(3n){
  border-right: none;
}
.keyboard .keyboard-content .keys .key:nth-child(n+10){
  border-bottom: none
}
.keyboard .keyboard-content .keys span:nth-of-type(1){
  font-weight: 600;
  font-size: 0.40rem;
}
.active{
  background-color: #aaaa;
}
.empty,.del{
  background-color: #d1d5dc;
}
