<template>
  <article class="withdraw-record-detail">
    <back-head>
      <span class="back-content">提现记录详情</span>
    </back-head>
    <section class="withdraw-record-detail-order-content">
      <div class="withdraw-record-detail-order-content-top">
        <p>3000.00</p>
      </div>
      <div class="withdraw-record-detail-order-content-explain">
        <span>提现说明</span>
        <p>提现到云付通余额</p>
      </div>
      <div class="withdraw-record-detail-order-content-process">
        <span>处理进度</span>
        <div class="withdraw-process">
          <div>
            <p>08-30 14:52</p>
            <span>申请收益提现12</span>
          </div>
          <div>
            <p>08-30 14:52</p>
            <span>申请收益提现</span>
          </div>
          <div>
            <span>提现中</span>
          </div>
        </div>
      </div>
      <div class="withdraw-record-detail-order-content-address">
        <span>提现到</span>
        <p>云付通余额(4546)</p>
      </div>
    </section>
    <section class="withdraw-record-detail-order-info">
      <div>
        <span>创建时间</span>
        <p>2018-08-03 14:32</p>
      </div>
      <div>
        <span>订单号</span>
        <p>2382982080982938</p>
      </div>
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  components: {
    BackHead
  }
}
</script>
<style lang="scss" scoped>
@import '~styles/common.scss';

.withdraw-record-detail-order-content {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  height: 5.6rem;
  margin-top: 0.3rem;
  font-size: 0.32rem;
  padding-left: 0.35rem;
  padding-right: 0.31rem;
  .withdraw-record-detail-order-content-top {
    p {
      height: 1.5rem;
      line-height: 1.5rem;
      text-align: center;
      font-weight: 600;
      font-size: 0.44rem;
    }
  }
  .withdraw-record-detail-order-content-explain,
  .withdraw-record-detail-order-content-process,
  .withdraw-record-detail-order-content-address {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .withdraw-record-detail-order-content-explain {
    height: 0.55rem;
    line-height: 0.55rem;
  }
  .withdraw-record-detail-order-content-process {
    display: flex;
    padding-top: 0.3rem;
    .withdraw-process {
      div {
        display: flex;
        padding-bottom: 0.5rem;
        color: #979797;
        p {
          padding-right: 0.25rem;
          font-size: 0.28rem;
          line-height: 0.4rem;
        }
        span {
          position: relative;
          font-size: 0.28rem;
          color: #54b6fc;
          width: 2rem;
          margin-left: 0.25rem;
          line-height: 0.4rem;
          &::before {
            position: absolute;
            top: 50%;
            left: -0.25rem;
            transform: translate(-0.08rem, -50%);
            content: '';
            display: inline-block;
            width: 0.17rem;
            height: 0.17rem;
            border-radius: 50%;
            background-color: #54b6fc;
          }
        }
        &:nth-of-type(1),
        &:nth-of-type(2) {
          span {
            &::after {
              position: absolute;
              top: 50%;
              left: -0.25rem;
              transform: translateY(0.08rem);
              content: '';
              display: inline-block;
              height: 0.75rem;
              width: 1px;
              background-color: #54b6fc;
            }
          }
        }
        &:nth-of-type(2){
          span{
            &::after{
              background-color: #aba5a6;
            }
          }
        }
        &:last-child {
          justify-content: flex-end;
          span{
            color: #aba5a6;
            &::before{
              background-color: #aba5a6;
            }
          }
        }
      }
    }
  }
  .withdraw-record-detail-order-content-address {
    height: 1.02rem;
    line-height: 1.02rem;
  }
}
.withdraw-record-detail-order-info {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  margin-top: 0.3rem;
  height: 1.88rem;
  div {
    flex: 1;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-size: 0.32rem;
    margin: 0 0.35rem;
    span{
      color: #676767;
    }
  }
}
</style>



