<template>
  <article class="payment-verification-result">
    <back-head>
      <span class="back-content">升级票务会员</span>
    </back-head>
    <section class="payment-verification-result-content">
      <div class="content-top">
        <img src="../../../assets/images/icon/tubiao_142.png" alt="">
        <span>恭喜您，实名认证成功</span>
      </div>
      <p>分享推荐您的好友,成为您的下级会员吧</p>
      <span>点击下载云返票务APP</span>
    </section>
    <foot :config='config'></foot>
    <keyboard :isShow='show'></keyboard>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  data () {
    return {
      config: {
        msg: '去推荐',
        isActive: true
        // nextComponentName: 'VerifyThePayment'
      },
      show: false,
      hasSetPassword: false // 是否设置支付密码
    }
  },
  components: {
    BackHead
  },
  methods: {}
}
</script>
<style lang='scss' scoped>
@import '~styles/common.scss';
.payment-verification-result {
  display: flex;
  background-color: #fff;
  flex-direction: column;
  height: 100%;
}
.payment-verification-result-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  .content-top {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1.83rem;
    img {
      width: 2.7rem;
      height: 2.5rem;
      margin-bottom: 0.27rem;
    }
    &>span{
      color: #ff350a;
    }
  }
  &>p{
    color: #a3a3a3;
    margin-top: 0.4rem;
  }
  &>span{
    display: inline-block;
    width: 3.66rem;
    height: 0.69rem;
    color: #1bbbf2;
    border:1px solid #1bbbf2;
    line-height: 0.69rem;
    text-align: center;
    font-size: 0.28rem;
    border-radius: 2vw;
    margin-top: 0.2rem;
  }
}
</style>



