<template>
  <article class="withdraw-password">
    <back-head>
      <span class="back-content">提现密码</span>
    </back-head>
    <section class="withdraw-password-content">
      <div class="withdraw-password-content-top">
        <img src="../../../../../assets/images/icon/tubiao_119.png" alt="">
        <div>
          <p>您是否还记得账号2382***5345  </p>
          <p>当前使用的密码</p>
        </div>
      </div>
    </section>
    <footer>
      <section class="withdraw-password-content-bottom">
        <!-- <router-link :to="{name:'DontRember'}" class="mui-btn mui-btn-danger">不记得</router-link>
        <router-link :to="{name:'Rember'}" class="mui-btn mui-btn-primary">记得</router-link> -->
        <span @tap="$router.push({name:'DontRember'})">不记得</span>
        <span @tap="$router.push({name:'Rember'})">记得</span>
      </section>
    </footer>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  components: {
    BackHead
  }
}
</script>
<style lang="scss" scoped>
@import '~styles/common.scss';
.withdraw-password{
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
}
.withdraw-password-content{
  flex:1;
  display: flex;
  flex-direction: column;
  .withdraw-password-content-top{
    flex: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 1.95rem;
    img{
      width: 4.82rem;
      height: 2.68rem;
    }
    &>div{
      padding-top: 0.4rem;
      text-align: center;
      color: #676767;
      p{
        padding: 0.1rem 0;
      }
    }
  }
}
footer{
  .withdraw-password-content-bottom{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
    border-top: 1px solid #d7d7d7;
    height: 0.89rem;
    box-sizing: border-box;
    span{
      flex: 1;
      line-height: 0.89rem;
      text-align: center;
      color: #666;
    }
    span:nth-of-type(2){
      color: #b404de;
      border-left:1px solid #d7d7d7;
    }
 }
}

</style>



