          <img src="../../../assets/images/shareicon/shareicon_05.png" alt="">
        </div>
        <div class="item" @click.prevent="shareweixin">
          <img src="../../../assets/images/shareicon/shareicon_07.png" alt="">
        </div>
        <div class="item">
          <img src="../../../assets/images/shareicon/shareicon_09.png" alt="">
        </div>
        <div class="item">
          <img src="../../../assets/images/shareicon/shareicon_11.png" alt="">
        </div>
        <div class="item">
          <img src="../../../assets/images/shareicon/shareicon_11.png" alt="">
        </div>
        <div class="item">
          <img src="../../../assets/images/shareicon/shareicon_11.png" alt="">
        </div>
        <div class="item">
