        </div>
      </div>
    </section>
    <!-- <section class="explain" :class="{'pop-active':isShowExplain}" ref="explain">
      <section class="explain-content">
        <p class="content-title">收益说明</p>
        <div class="content-text-wrapper">
          <div class="content-text">
            <p>升级收益</p>
            <p>上级有10%收益</p>
          </div>
