<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  components: {
    BackHead
  },
  data () {
    return {
      a: 'anme',
      isHideToatalAcount: false,
      totalAcount: 1000,
      imgSrc: require('../../../assets/images/icon/tubiao_31.png'),
      rightIcon: require('../../../assets/images/icon/tubiao_89.png'),
      mask: this.$mui.createMask(this.keepMaskShow),
      contentList: [
        {
          itemSrc: require('../../../assets/images/icon/tubiao_122.png'),
          content: '收益提现',
          info: '',
          keys: 'withdraw',
          name: 'Withdraw'
        },
        {
          itemSrc: require('../../../assets/images/icon/tubiao_129.png'),
          content: '收益明细',
          info: '',
          keys: 'income',
          name: 'IncomeList'
        },
        {
          itemSrc: require('../../../assets/images/icon/tubiao_139.png'),
          content: '我的资料',
          info: '17723821897',
          keys: 'profile',
          name: 'MyProfile'
        },
        {
          itemSrc: require('../../../assets/images/icon/tubiao_153.png'),
          content: '下级会员',
          info: '10',
          keys: 'subord',
          name: 'SubordinateMember'
        }
      ],
      isShowShareWays: false, // 控制是否显示分享提示框
      testTitle: ''
    }
  },
  methods: {
    showExplain () {
      this.$alert({
        title: '收益说明',
        content: '',
        hasBtn: false,
        isUseExplain: true
      })
    },
    isHideAcount () {
      this.isHideToatalAcount = !this.isHideToatalAcount
      this.totalAcount = this.isHideToatalAcount ? '*****' : '10000.00'
      this.imgSrc = require(this.isHideToatalAcount
        ? '../../../assets/images/icon/tubiao_42.png'
        : '../../../assets/images/icon/tubiao_31.png')
    },
    showMenu () {
      this.isShowShareWays = true
    },
    hideShareWays () {
      this.isShowShareWays = false
    },
    shareWays () {
      this.$mui('.menu').popover('hide', this.$refs.openPopover) // 把菜单弹出框隐藏
      this.mask.show()
      this.isShowShareWays = !this.isShowShareWays
    },
    closeExplain () {
      this.mask.close()
    },
    keepMaskShow () {
      // 点击(mask)蒙板不消失,只有点击关闭按钮才消失
      if (event.target.className !== 'close') {
        return false
      }
    },
    shareweixin () {
      console.log('inShare')
      this.$api
        .weixin({share_url: 'http://www.baidu.com'})
        .then(res => {
          this.testTitle = res.data.appId
          this.$wx.config({
            debug: false,
            appId: res.data.appId,
            timestamp: res.data.timestamp,
            nonceStr: res.data.nonceStr,
            signature: res.data.signature,
            jsApiList: [
              'checkJsApi',
              'onMenuShareTimeline',
              'onMenuShareAppMessage',
              'onMenuShareQQ'
            ]
          })
          this.$wx.ready(function () {
            var shareData = {
              title: '标题',
              desc: '测试描述',
              link: res.data.url,
              imgUrl: ''
            }
            this.$wx.onMenuShareAppMessage(shareData)
            this.$wx.onMenuShareTimeline(shareData)
            this.$wx.onMenuShareQQ(shareData)
          })
        })
      // $.ajax({
      //   url: '/signture',
      //   type: 'post',
      //   data: { url: location.href.split('#')[0] },
      //   success: function (res) {
      //     wx.config({
      //       debug: false,
      //       appId: 'wxa2c416de84300ee5',
      //       timestamp: res.timestamp,
      //       nonceStr: res.nonceStr,
      //       signature: res.signature,
      //       jsApiList: [
      //         'checkJsApi',
      //         'onMenuShareTimeline',
      //         'onMenuShareAppMessage',
      //         'onMenuShareQQ'
      //       ]
      //     })
      //     wx.ready(function () {
      //       var shareData = {
      //         title: document.title,
      //         desc: getDesc(),
      //         link: res.url,
      //         imgUrl: getImage()
      //       }
      //       wx.onMenuShareAppMessage(shareData)
      //       wx.onMenuShareTimeline(shareData)
      //       wx.onMenuShareQQ(shareData)
      //     })
      //     wx.error(function (res) {
      //       alert(res.errMsg) // 正式环境记得关闭啊！！！！
      //     })
      //   }
      // })
      // // 获取描述字段方法
      // function getDesc () {
      //   var meta = document.getElementsByTagName('meta')
      //   for (var i = 0; i < meta.length; i++) {
      //     if (
      //       typeof meta[i].name !== 'undefined' &&
      //       meta[i].name.toLowerCase() === 'description'
      //     ) {
      //       return meta[i].content
      //     }
      //   }
      // }
      // // 获取图片
      // function getImage () {
      //   return 'http://' + location.host + '/images/logo.png'
      // }
    },
    copyLink () {
      console.log(document.execCommand)
    }
  }
}
</script>
