          padding: 0rem 0.32rem 0rem 0.4rem;
          img {
            width: 0.27rem;
            height: 0.32rem;
            margin-right: 0.12rem;
          }
          .content {
            flex: 1;
            display: flex;
            justify-content: space-between;
            border-top: 1px solid #e6e6e6;
            padding-top: 0.09rem;
            // padding-bottom: 0.0rem;
