.popup-link {
  display: none;
  background: #fff;
  position: absolute;
  top: 2.3rem;
  left: 50%;
  transform: translate(-3rem, 0);
  z-index: 999;
  .wrapper {
    display: flex;
    flex-direction: column;
    width: 6rem;
    height: 4.3rem;
    padding: 0.3rem 0.1rem 0.1rem;
    margin: auto;
    textarea {
      border: 1px solid #cccccc;
    }
    p {
      flex: 1;
    }
    span {
      box-sizing: border-box;
      text-align: center;
      width: 1.6rem;
      height: 0.6rem;
      line-height: 0.6rem;
      border: 1px solid #ccc;
      border-radius: 5px;
      background: #fff;
      align-self: center;
    }
  }
