.pop-active {
  display: block !important;
}
.share-ways {
  position: fixed;
  display: none;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  z-index: 999;
  .item-wrapper {
    position: absolute;
    bottom: 0;
    display: flex;
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    // height: 4.2rem;
    padding-top: 0.82rem;
    padding-bottom: 0.23rem;
    background: #fff;
    .item {
      flex: 0 1 20%;
      text-align: center;
      margin-bottom: 0.3rem;
      img {
        width: 1.03rem;
        height: 1.15rem;
      }
    }
  }
}
.mask {
  background: rgba(0, 0, 0, 0.7);
}
