<template>
  <div class="foot-next" @tap="goNext" :class="{'footIsActive':isActive}">
  <!-- <div class="foot-next" @click="fn"> -->
    下一步
  </div>
</template>
<script>
export default {
  props: ['isActive', 'componentName'],
  data () {
    return {

    }
  },
  methods: {
    goNext () {
      if (this.isActive) {
        this.$router.push({name: this.componentName})
      } else {
        return false
      }
    }
  }
}
</script>
<style lang='scss' scoped>
@import '~styles/common.scss';
.foot-next{
  box-sizing: border-box;
  height: 0.9rem;
  border-top: 1px solid #d7d7d7;
  line-height: 0.9rem;
  text-align: center;
}
.footIsActive{
   background-color:$backColor;
   color: #ffffff;
}
</style>



