<template>
  <article class="person-information">
    <back-head>
      <span class="back-content">实名信息</span>
    </back-head>
    <section class="person-information-content">
      <div class="head-portrait">
        <p>姓名</p>
        <span>王昭君</span>
      </div>
      <div class="account-number">
        <p>性别</p>
        <span>王昭君</span>
      </div>
      <div class="nickname">
        <p>证件类型</p>
        <span>身份证</span>
      </div>
      <div class="sex">
        <p>证件号码</p>
        <span>4***************2</span>
      </div>
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  data () {
    return {}
  },
  components: {
    BackHead
  }
}
</script>
<style lang='scss' scoped>
@import '~styles/common.scss';
.person-information-content {
  display: flex;
  flex-direction: column;
  margin-top: 0.3rem;
  padding-left: 0.38rem;
  height: 3.36rem;
  background-color: #fff;
  div {
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-right: 0.38rem;
    span{
      color: #999;
    }
  }
  div:not(:last-of-type){
    border-bottom: 1px solid #e4e4e4;
  }
  img {
    width: 0.6rem;
    height: 0.6rem;
    border: 1px solid #ccc;
    border-radius: 50%;
  }
}
</style>



