<template>
  <article class="set-password">
    <back-head>
      <span class="back-content">设置密码页</span>
    </back-head>
    <section class="set-password-content">
      <div class="tips">
        <p>请为账号238****234</p>
        <span>设置6位数字支付密码</span>
      </div>
      <div class="set-password-content-password-wrapper" ref="codeWrapper">
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
      </div>
      <p>支付密码不能是重复、连续的数字</p>
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
import FootNext from 'commonComponent/footNext/FootNext'
export default {
  components: {
    BackHead,
    FootNext
  },
  data () {
    return {
      verificationCode: []
    }
  },
  methods: {
    watchInp (event) {
      var e = event || window.event
      var target = e.target || e.srcElement
      var inps = this.$refs.codeWrapper.children
      var isNumber = /[\d]/.test(target.value)
      if (isNumber) {
        for (var i = 0; i < inps.length; i++) {
          if (target === inps[i]) {
            if (this.verificationCode['num' + i] === 'done') {
              target.value = target.value[1]
            }
            this.$set(this.verificationCode, i, target.value)
            this.verificationCode['num' + i] = 'done'
            if (
              i < inps.length - 1 &&
              this.verificationCode['num' + (i + 1)] !== 'done'
            ) {
              // 当下一个输入框没有有效值时，获取焦点
              inps[i + 1].focus()
              this.verificationCode['num' + (i + 1)] = 'will'
            }
            // 当所有输入框的值都有效时，调用验证密码格式函数
            var codeStr = this.verificationCode.join('')
            if (codeStr.length === 6) {
              // 输入密码到了六位后，调用验证函数，判断密码是否符合规范
              this.verifyCode(codeStr)
            }
          }
        }
      } else {
        target.value = ''
        // event.preventDefault()
      }
    },
    deleteCode (event) {
      var e = event || window.event
      var target = e.target || e.srcElement
      var inps = this.$refs.codeWrapper.children
      for (var i = 0; i < inps.length; i++) {
        if (target === inps[i] && i >= 0) {
          // ['num'+i] 属性有四个值，表示当前输入的四个状态：
          // will：刚获得焦点，未输入值
          // empty: 获得焦点后，输入了值，然后被删除了值
          // clear: 失去焦点并且没有输入值
          // done: 输入框有有效值
          // 第一次点击删除键，删除内容，并给对应的['num'+i]赋值为empty
          // empty 表示原先已有内容，现在删除了，
          // 第二次点击删除键，则退回上一个输入框，并把['num'+i]赋值为'claer'
          // 当焦点自动跳转到当前输入框，此时输入框没有内容，点击删除键(第一次)
          this.$set(this.verificationCode, i, target.value)
          if (
            this.verificationCode['num' + i] === 'empty' ||
            this.verificationCode['num' + i] === 'will'
          ) {
            if (i !== 0) {
              inps[i - 1].focus()
            }
            this.verificationCode['num' + i] = 'cealr'
          } else {
            this.verificationCode['num' + i] = 'empty'
          }
        }
      }
    },
    verifyCode (code) {
      var start = this.verificationCode[0] - 0
      var end = this.verificationCode[5] - 0
      var isRepeat =
        this.unique(this.verificationCode).length === 1 ? 'true' : 'false'
      if (end - start === 5) {
        this.$mui.toast('密码不能连续', { type: 'div' })
        document.getElementsByClassName('mui-toast-container')[0].className +=
          ' message-middle'
      } else if (isRepeat === 'true') {
        this.$mui.toast('密码不能重复', { type: 'div' })
        document.getElementsByClassName('mui-toast-container')[0].className +=
          ' message-middle'
      } else {
        this.$router.push({ name: 'SetPasswordConfirm', params: {password: this.verificationCode} })
      }
    },
    unique (arr) {
      // 判断数组是否有重复的，用来判断密码是否为6为重复的
      var tmp = {}
      var result = []
      var len = arr.length
      for (let i = 0; i < len; i++) {
        if (!tmp[arr[i]]) {
          tmp[arr[i]] = 'c'
          result.push(arr[i])
        }
      }
      return result
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~styles/common.scss';
.set-password {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #f7f7f7;
  overflow: hidden;
}
.set-password-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  & > p {
    align-self: center;
    color: #9c9c9c;
  }
  .tips {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 0.7rem;
    color: #000;
    p {
      font-size: 0.32rem;
      color: #000;
    }
  }
  .set-password-content-password-wrapper {
    width: 100%;
    padding: 0;
    display: flex;
    justify-content: center;
    margin-top: 0.38rem;
    margin-bottom: 0.46rem;
    & > input {
      box-sizing: border-box;
      width: 0.74rem;
      height: 0.74rem;
      border-radius: 0;
      text-align: center;
      border: 1px solid #b3b3b3;
      border-right: none;
      &:last-child {
        border-right: 1px solid #b3b3b3;
      }
    }
  }
}
</style>



