<template>
  <div class="verify-the-payment">
    <back-head>
      <span class="back-content">支付验证</span>
    </back-head>

    <keyboard>
      <div class="verify-the-payment-content">
        <section class="head">
          <span class="mui-icon mui-icon-back"></span>
          <p>请输入支付密码</p>
        </section>
        <section class="middle">
          <p>票务余额 : </p>
          <span> 0.00</span>
        </section>
      </div>
    </keyboard>
  </div>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
import Keyboard from 'commonComponent/keyboard/Keyboard'
export default {
  components: {
    BackHead,
    Keyboard
  },
  data () {
    return {
      verificationCode: []
    }
  }
}
</script>
<style lang='scss' scoped>
@import '~styles/common.scss';
.verify-the-payment{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  background-color: #808080;
}
.verify-the-payment-content {
  display: flex;
  flex-direction: column;
  // justify-content: flex-end;
  // height: calc(100vh - 1rem);
  .head,
  .middle {
    display: flex;
    flex-direction: row;
  }
  .head {
    padding: 0.2rem 0;
    border-bottom: 1px solid #aaaaaa;
    span {
      padding-left: 0.2rem;
      font-weight: 600;
    }
    p {
      font-weight: 600;
      font-size: 0.32rem;
      flex: 1;
      text-align: center;
    }
  }
  .middle {
    padding: 0.1rem 0;
    p {
      padding-left: 0.3rem;
    }
    span {
      padding-left: 0.1rem;
    }
  }
}
</style>



