const isPro = Object.is(process.env.NODE_ENV, 'production')

const Axios = axios.create({
  // baseURL: '/', // 因为设置了本地代理,所以baseURL为'/'或''，如果改为其他的值，则请求时，会改变代理的地址。
  baseURL: isPro ? 'http://console.infunpw.com/api/' : 'api/', // 因为设置了本地代理,所以baseURL为'/'或''，如果改为其他的值，则请求时，会改变代理的地址。
  timeout: 30000,
  responseType: 'json',
  withCredentials: true, // 是否允许携带cookie
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
  }
})
// 请求拦截：post传参序列化
Axios.interceptors.request.use(config => {
  if (config.method === 'post') {
    // 序列化
    // console.log('a', config.data)
    config.data = qs.stringify(config.data, {arrayFormat: 'indices', allowDots: true})
    // 温馨提示,若是能直接接受json 格式,可以不用 qs 来序列化的
  }
  // 若是有做鉴权token , 就给头部带上token
  // 若是需要跨站点,存放到 cookie 会好一点,限制也没那么多,有些浏览环境限制了 localstorage 的使用
  // 这里localStorage一般是请求成功后我们自行写入到本地的,因为你放在vuex刷新就没了
  // 一些必要的数据写入本地,优先从本地读取
  if (localStorage.token) {
    config.headers.Authorization = localStorage.token
  }
  // console.log('请求', config)
  return config
}, error => {
  return Promise.reject(error)
})

// 返回状态判断(添加响应拦截器)
Axios.interceptors.response.use(
  res => {
    // 对响应数据做些事
    return res
  },
  error => {
    // 用户登录的时候会拿到一个基础信息,比如用户名,token,过期时间戳
    // 直接丢localStorage或者sessionStorage
    if (!window.localStorage.getItem('loginUserBaseInfo')) {
      // 若是接口访问的时候没有发现有鉴权的基础信息,直接返回登录页

    } else {
      // 若是有基础信息的情况下,判断时间戳和当前的时间,若是当前的时间大于服务器过期的时间
      // 乖乖的返回去登录页重新登录
      let lifeTime =
        JSON.parse(window.localStorage.getItem('loginUserBaseInfo')).lifeTime *
        1000
      let nowTime = new Date().getTime() // 当前时间的时间戳
      console.log(nowTime, lifeTime)
      console.log(nowTime > lifeTime)
      if (nowTime > lifeTime) {
        // 登录过期
      } else {
        // 下面是接口回调的satus ,因为我做了一些错误页面,所以都会指向对应的报错页面
        // if (error.response.status === 403) {
        //   router.push({
        //     path: '/error/403'
        //   })
        // }
        // if (error.response.status === 500) {
        //   router.push({
        //     path: '/error/500'
        //   })
        // }
        // if (error.response.status === 502) {
        //   router.push({
        //     path: '/error/502'
        //   })
        // }
        // if (error.response.status === 404) {
        //   router.push({
        //     path: '/error/404'
        //   })
        // }
      }
    }
    // 返回 response 里的错误信息
    // console.log(error.data)
    let errorInfo = error.data ? 'error.data.error.message' : 'error.data'
    return Promise.reject(errorInfo)
  }
)

// 对axios的实例重新封装成一个plugin ,方便 Vue.use(xxxx)
// export default {
//   install: function (Vue, Option) {
//     Object.defineProperty(Vue.prototype, '$http', { value: Axios })
//   }
// }

export default Axios



