<template>
  <article class="identification-card-verification">
    <back-head>
      <span class="back-content">验证码</span>
    </back-head>
    <section class="identification-card-verification-content">
      <div class="tips">
        <p>填写*君身份证号码</p>
      </div>
      <div class="identification-card-verification-content-input" ref="codeWrapper">
        <input type="text" v-model="idCardNumber" maxlength="18">
      </div>
      <!-- <router-link :to="{name:'SetPassword'}" class="mui-btn next">下一步</router-link> -->
      <router-link class="identification-card-verification-content-change-way" :to="{name:'DontRember'}">换个方式重置密码</router-link>
    </section>
    <footer>
      <foot-next :is-active="canNext" :component-name="'SetPassword'"></foot-next>
    </footer>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
import FootNext from 'commonComponent/footNext/FootNext'
export default {
  components: {
    BackHead,
    FootNext
  },
  data () {
    return {
      canNext: false,
      idCardNumber: ''
    }
  },
  watch: {
    idCardNumber: function (val) {
      if (val.trim().length === 18) {
        this.canNext = true
      } else {
        this.canNext = false
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~styles/common.scss';
.identification-card-verification{
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
  background-color: #f7f7f7;
}
.identification-card-verification-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  .tips {
    margin-top: 0.7rem;
    p{
      text-align: center;
      font-size: 0.32rem;
    }
  }
  .identification-card-verification-content-input {
    margin:0.4rem auto 0.45rem;
    input{
      width: 6.54rem;
      border: none;
      border-bottom: 1px solid #b3b3b3;
      background-color: #f7f7f7;
      margin: 0;
      padding: 0;
    }
  }
  .identification-card-verification-content-change-way {
    align-self: center;
    color: #169dff;
  }
}
</style>



