<template>
  <article class="person-data-not-active">
    <back-head>
      <span class="back-content">个人资料</span>
    </back-head>
    <section class="person-data-not-active-content" v-if="!isLoading">
      <div class="head-portrait">
        <p>票务头像</p>
        <img src="" alt="">
      </div>
      <div class="account-number">
        <p>账号</p>
        <span v-text="personData.account">23982</span>
      </div>
      <div class="nickname">
        <p>昵称</p>
        <span v-text="personData.nickname">奇了怪啊s</span>
      </div>
      <div class="phone">
        <p>手机号</p>
        <span v-text="personData.mobile">19784464865</span>
      </div>
      <div class="sex">
        <p>性别</p>
        <span v-text="personData.sex === 1? '男': '女'">女</span>
      </div>
    </section>
    <section v-else>
      加载中.....
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  data () {
    return {
      personData: null,
      isLoading: true // 判断是否还在加载数据
    }
  },
  components: {
    BackHead
  },
  methods: {
    getPersonData () { // 第一次进入，通过路由传参获取数据，如果被刷新，则通过请求获取信息
      if (this.$route.params.personData) {
        console.log('pa', this.$route.params.personData)
        this.personData = this.$route.params.personData
        this.isLoading = false
      } else {
        this.$api.getUserInfo().then(res => {
          console.log('er', res.data.errorCode)
          if (res.data.errorCode === 0) {
            this.personData = res.data.data
            this.isLoading = false
          }
        })
      }
    }
  },
  created () {
    this.getPersonData()
  }
}
</script>
<style lang='scss' scoped>
@import '~styles/common.scss';
.person-data-not-active-content {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  margin-top: 0.3rem;
  padding-left: 0.42rem;
  // fon
  div {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 0.78rem;
    padding-right: 0.36rem;
  }
  div:not(:last-of-type){
    border-bottom: 1px solid #e4e4e4;
  }
  img {
    width: 0.55rem;
    height: 0.55rem;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
}
</style>



