<template>
  <article class="share-not-opened">
    <back-head>
      <span class="back-content">分享系统</span>
      <div class="back-right-icon">
        <span class="mui-icon mui-icon-help" @click="showAlert"></span>
      </div>
    </back-head>
    <section class="my-share-top">
      <div class="my-share-top-temshare">
        <p>您暂未开通权限，请先开通权限</p>
        <p>即可享受会员分享收益</p>
      </div>
      <div class="my-share-top-totalshare">
        <div>
          <img src="../../../assets/images/icon/tubiao_86.png" alt="">
          <p @click="toUpgradeMembership">升级票务会员</p>
        </div>
      </div>
    </section>
    <section class="my-share-content">
      <div class="user-info" >
        <img src="../../../assets/images/icon/tubiao_84.png" alt="">
        <p>我的资料</p>
        <div @click="$router.push({name:'MyProfileNotActive', params:{isActive: false}})">
          <span class="referrer">13573829872</span>
          <span class="right-arrows"></span>
        </div>
      </div>
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
import {mapState} from 'vuex'
export default {
  components: {
    BackHead
  },
  data () {
    return {
      a: 'anme',
      isLogin: false,
      isHideToatalAcount: false,
      totalAcount: 1000
    }
  },
  methods: {
    toUpgradeMembership () {
      this.$api.checkIdauthStatus().then(res => {
        if (res.data.errorCode === 0) {
          if (res.data.data.is_idauth === 1) {
            this.$router.push({ name: 'UpgradeMembershipRN' })
          } else {
            this.$router.push({ name: 'UpgradeMembershipNRN' })
          }
        }
      })
    },
    showAlert () {
      this.$alert({
        title: '费用说明',
        hasBtn: false,
        isUseExplain: true
      })
    }
  },
  computed: mapState(['isMember'])
}
</script>

<style lang='scss' scoped>
@import '~styles/common.scss';
.share-not-opened /deep/ .myHead {
  ul {
    background-image: url('../../../assets/images/bg/bg2.jpg');
    background-size: cover;
  }
  .back-right-icon {
    padding-right: 0.25rem;
  }
}
.my-share-top {
  height: 3.08rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-image: url('../../../assets/images/bg/bg2.jpg');
  background-size: 100% 3.96rem;
  background-repeat: no-repeat;
  background-position: 0 -0.88rem;
  color: #dbb2da;
  .my-share-top-temshare {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .my-share-top-totalshare {
    display: flex;
    flex-direction: row;
    justify-content: center;
    & > div {
      display: flex;
      flex-direction: row;
      width: 2.96rem;
      height: 0.64rem;
      background-color: rgba(101, 107, 204, 0.8);
      align-items: center;
      color: #fff;
      border-radius: 5px;
      margin-top: 0.4rem;
      img {
        width: 0.37rem;
        height: 0.34rem;
        margin-left: 0.25rem;
        margin-right: 0.13rem;
      }
      p {
        height: 0.38rem;
        line-height: 0.38rem;
      }
    }
  }
}
.my-share-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1;
  .user-info {
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 0.9rem;
    background-color: #fff;
    img{
      width: 0.44rem;
      height: 0.32rem;
      margin-left: 0.26rem;
      margin-right: 0.14rem;
    }
    &>p{
      flex: 1;
    }
    div{
      display: flex;
      align-items: center;
      justify-content: flex-end;
      p {
        font-size:0.32rem;
      }
    }
  }
}
.right-arrows {
  width: 0.18rem;
  height: 0.3rem;
  background-image: url('../../../assets/images/icon/tubiao_89.png');
  background-size: contain;
  background-repeat: no-repeat;
  margin-right: 0.28rem;
  margin-left: 0.16rem;
}
</style>



