<template>
  <article class="dont-rember">
    <back-head>
      <span class="back-content">重置密码</span>
    </back-head>
    <section class="dont-rember-content">
      <ul>
        <li @tap="$router.push({name:'Rember'})">
          <span>支付密码</span>
          <img src="../../../../../assets/images/icon/tubiao_89.png" alt="">
        </li>
        <li @tap="$router.push({name:'PhoneVerificationCode'})">
          <span>短信验证码+身份证件</span>
          <img src="../../../../../assets/images/icon/tubiao_89.png" alt="">
        </li>
      </ul>
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  components: {
    BackHead
  }
}
</script>
<style lang="scss" scoped>
@import '~styles/common.scss';
.dont-rember{
  height: 100vh;
  overflow: hidden;
  background-color: #f7f7f7;
}
.dont-rember-content{
  margin-top: 0.3rem;
  ul{
    background-color: #ffffff;
    padding-left: 0.35rem;
    li{
      display: flex;
      flex-direction: row;
      height: 0.9rem;
      box-sizing: border-box;
      border-bottom: 1px solid #e3e3e3;
      align-items: center;
      span{
        flex: 1;
      }
      img{
        width: 0.19rem;
        height: 0.29rem;
        margin-right: 0.26rem;
      }
    }
  }
}
</style>



