<template>
  <article class="person-data">
    <back-head>
      <span class="back-content">个人资料</span>
    </back-head>
    <section class="person-data-content">
      <div class="head-portrait">
        <p>票务头像</p>
        <img src="" alt="">
      </div>
      <div class="account-number">
        <p>账号</p>
        <span>23982</span>
      </div>
      <div class="nickname">
        <p>昵称</p>
        <span>奇了怪啊</span>
      </div>
      <div class="phone">
        <p>手机号</p>
        <span>15177552233</span>
        <span class="right-arrows" @click="modifyPhoneNumber"></span>
      </div>
      <div class="sex">
        <p>性别</p>
        <span>女</span>
      </div>
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  data () {
    return {}
  },
  components: {
    BackHead
  },
  methods: {
    modifyPhoneNumber () {
      this.$mui.prompt('', '123452145', '修改手机号', function () {
        console.log(1)
      })
    }
  }
}
</script>
<style lang='scss' scoped>
@import '~styles/common.scss';
.person-data-content {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  margin-top: 0.3rem;
  padding-left: 0.42rem;
  // fon
  div {
    display: flex;
    flex-direction: row;
    align-items: center;
    // justify-content: space-between;
    height: 0.78rem;
    padding-right: 0.36rem;
    p{
      flex: 1;
    }
  }
  div:not(:last-of-type){
    border-bottom: 1px solid #e4e4e4;
  }
  img {
    width: 0.55rem;
    height: 0.55rem;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
}
.right-arrows {
  width: 0.18rem;
  height: 0.3rem;
  background-image: url('../../../../assets/images/icon/tubiao_89.png');
  background-size: contain;
  background-repeat: no-repeat;
  // margin-right: 0.26rem;
  margin-left: 0.16rem;
}
</style>



