<template>
  <article class="my-profile-not-active">
    <back-head>
      <span class="back-content">我的资料</span>
    </back-head>
    <section class="my-profile-not-active-content" v-if="!isLoading">
      <div class="my-profile-not-active-content-head">
        <img src="" alt="">
        <div class="head-info">
          <p v-text="userInfo.nickname">昵称昵称</p>
          <span>账号：{{userInfo.account}}</span>
        </div>
        <div class="arrows-wrapper" @click="$router.push({name:'PersonDataNotActive', params: {personData: userInfo}})">
          <span class="right-arrows"></span>
        </div>
      </div>
      <div class="my-profile-not-active-content-middle">
        <div class="real-name-authentication">
          <img src="../../../../assets/images/icon/tubiao_72.png" alt="">
          <p>实名认证</p>
          <img src="../../../../assets/images/icon/tubiao_77.png" alt="">
        </div>
        <div class="my-references">
            <img src="../../../../assets/images/icon/tubiao_83.png" alt="">
            <p>我的推荐人</p>
          <div @click="$router.push({name:'MyReferencesNotActive'})">
            <span class="referrer">无推荐人</span>
            <span class="right-arrows"></span>
          </div>
        </div>
      </div>
    </section>
    <section v-else>
      加载中.....
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  data () {
    return {
      userInfo: null,
      isLoading: true
    }
  },
  components: {
    BackHead
  },
  methods: {},
  mounted () {},
  created () {
    this.$api.getUserInfo().then(res => {
      if (res.data.errorCode === 0) {
        this.userInfo = res.data.data
        this.isLoading = false
      }
    })
  }
}
</script>
<style lang='scss' scoped>
@import '~styles/common.scss';
.my-profile-not-active-content {
  display: flex;
  flex-direction: column;
  .my-profile-not-active-content-head {
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: #ffffff;
    height: 1.46rem;
    margin-top: 0.3rem;
    padding-left: 0.25rem;
    img {
      width: 0.95rem;
      height: 0.95rem;
      background-color: #888282;
      border-radius: 5px;
    }
    .head-info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 1rem;
      flex: 1;
      padding-left: 0.3rem;
      color: #000;
      span {
        font-size: 0.24rem;
      }
    }
    .arrows-wrapper {
      width: 1rem;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
  }
}
.my-profile-not-active-content-middle {
  display: flex;
  flex-direction: column;
  background: #ffffff;
  height: 1.88rem;
  margin-top: 0.28rem;
  padding-left: 0.38rem;
  &>div:nth-of-type(1) {
    border-bottom: 1px solid #e4e4e4;
  }
  &>div {
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    div{
      display: flex;
      align-items:center;
    }
    img {
      height: 0.35rem;
    }
    img:nth-of-type(1) {
      width: 0.42rem;
      margin-right: 0.13rem;
    }
    img:nth-of-type(2) {
      width: 0.96rem;
      margin-right: 0.26rem;
    }
    p {
      flex: 1;
      font-size: 0.32rem;
    }
    .referrer {
      color: #999;
    }
  }
  .real-name-authentication {
    .arrows-right {
      padding-left: 0.2rem;
    }
  }
  .my-references {
    .arrows-right {
      padding-left: 0.2rem;
    }
  }
}
.right-arrows {
  width: 0.18rem;
  height: 0.3rem;
  background-image: url('../../../../assets/images/icon/tubiao_89.png');
  background-size: contain;
  background-repeat: no-repeat;
  margin-right: 0.26rem;
  margin-left: 0.16rem;
}
</style>



