import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  isLogin: false,
  loginErrorCode: 2,
  isMember: false
}
const mutations = {
  changeLoginState (state, msg) { // 改变登录状态
    state.isLogin = msg === 0 ? true : Boolean(false)
  },
  changeMemberState (state, msg) { // 保留会员状态
    state.isMember = msg
  }
}
export default new Vuex.Store({
  state,
  mutations
})



