
<template>
  <article class="message-list">
    <back-head>
      <span class="back-content">消息列表</span>
    </back-head>
    <section class="message-list-content">
      <div class="list-item" v-for="item in messageList" :key="item.id">
        <!-- <span class="recommend-icon"></span> -->
        <span :class="iconClass(item.type)"></span>
        <div class="item-content">
          <div class="head">
            <p v-text="item.title" :class="titleClass(item.type)">推荐奖励</p>
            <span v-text="item.time">09-09</span>
          </div>
          <p v-text="item.content">亲爱的会员，您的分享会员15987978988已经开通推荐权限</p>
        </div>
      </div>
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  data () {
    return {
      messageList: [
        {
          type: 'tui',
          title: '推荐奖励',
          time: '09-09',
          content: '亲爱的会员，您的分享会员15987978988已经开通推荐权限',
          id: 'tui'
        },
        {
          type: 'ye',
          title: '推荐奖励',
          time: '09-09',
          content: '亲爱的会员，您的分享会员15987978988已经开通推荐权限',
          id: 'ye'
        }
      ]
    }
  },
  components: {
    BackHead
  },
  methods: {
    iconClass (type) {
      if (type === 'tui') {
        return 'recommend-icon'
      } else if (type === 'ye') {
        return 'professional-icon'
      }
    },
    titleClass (type) {
      if (type === 'tui') {
        return 'recommend-title-color'
      } else if (type === 'ye') {
        return 'professional-title-color'
      }
    }
  }
}
</script>
<style lang='scss' scoped>
@import '~styles/common.scss';
.message-list-content {
  .list-item {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    height: 1.8rem;
    padding: 0.3rem 0 0 0.28rem;
    &>span{
      align-self: flex-start;
    }
    .item-content {
      flex: 1;
      display: flex;
      border-bottom: 1px solid #d7d7d7;
      flex-direction: column;
      height: 100%;
      padding-right: 0.45rem;
      p{
        font-size: 0.28rem;
      }
      .head {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: 0.45rem;
        span{
          color: #dadada;
        }
      }
          &>p{
            color:#656565;
          }
    }
  }
}
.recommend-icon {
  width: 0.3rem;
  height: 0.35rem;
  background-image: url('../../../assets/images/icon/tubiao_28.png');
  background-size: contain;
  background-repeat: no-repeat;
  margin-right: 0.1rem;
}
.professional-icon {
  width: 0.3rem;
  height: 0.3rem;
  background-image: url('../../../assets/images/icon/tubiao_34.png');
  background-size: contain;
  background-repeat: no-repeat;
  margin-right: 0.1rem;
  padding-top: 0.01rem;
}
.recommend-title-color{
  color: #a915b0;
}
.professional-title-color{
  color: #ff2c00;
}
</style>



