<template>
  <div class="withdraw">
    <back-head>
      <span class="back-content">提现</span>
      <div class="back-right-icon">
        <img ref="openPopover" class="addIcon" src="../../../../assets/images/icon/tubiao_07.png" alt="" @click="showMenu">
      </div>
    </back-head>
    <article class="withdraw-wrapper">
      <section class="cash-account">
        <img src="../../../../assets/images/icon/tubiao_03.png" alt="">
        <div class="text">
          <p>云付通余额</p>
          <span>云付通账号 2382812378289</span>
        </div>
      </section>
      <section class="cash-withdraw-amount">
        <p>提现金额</p>
        <div class="input-amount">
          <span>￥</span>
          <input type="text">
        </div>
        <div class="available-balance">
          <span>可用余额</span>
          <span class="account">1000</span>
          <span>元</span>
        </div>
      </section>
      <section class="prompt-message">
        <p>1. 提现金额不得大于可用金额；</p>
        <p>2. 提现金额以100元为基数，整百提现；</p>
        <p>3. 提现金额在2-5个工作日内到账。</p>
      </section>
    </article>
    <article class="menu-wrapper">
      <div id="popover" class="mui-popover menu">
        <ul class="mui-table-view">
          <li class="mui-table-view-cell" @click="hideMask">
            <span class="mui-icon mui-icon-chatbubble"></span>
            <router-link :to="{name:'WithdrawPassword'}">提现密码</router-link>
          </li>
          <li class="mui-table-view-cell" @click="hideMask">
            <span class="mui-icon mui-icon-contact"></span>
            <router-link :to="{name:'WithdrawRecord'}">提现记录</router-link>
          </li>
        </ul>
      </div>
    </article>
    <!-- <footer>
      <button>提交申请</button>
    </footer> -->
  </div>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  components: {
    BackHead
  },
  methods: {
    showMenu () {
      this.$mui('.mui-popover').popover('toggle', this.$refs.openPopover)
    },
    hideMask () {
      // 去除弹出框跳转后未消失的背景层(遮罩层)
      var mask = document.getElementsByClassName('mui-backdrop mui-active')
      mask[0].remove('mui-active')
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~styles/common.scss';
.withdraw{
  height: 100vh;
  overflow: hidden;
  background-color: #f7f7f7;
}
.back-right-icon{
  .addIcon{
    width: 0.42rem;
    height: 0.42rem;
    margin-right: 0.26rem;
  }
}
.withdraw-wrapper {
  display: flex;
  flex-direction: column;
}
.menu-wrapper {
  .menu {
    width: 3.2rem;
    ul {
      height: 1.84rem;
      li {
        display: flex;
        flex-direction: row;
        box-sizing: content-box;
        height: 0.48rem;
        a {
          line-height: 0.48rem;
          margin-left: 0.1rem;
        }
      }
    }
  }
}
.cash-account {
  margin-top: 0.38rem;
  margin-bottom: 0.4rem;
  height: 1.4rem;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 0.4rem;
  img{
    width: 1.08rem;
    height: 1.08rem;
    border: 1px solid #d7d7d7;
    box-sizing: border-box;
  }
  .text {
    text-align: right;
    margin-right: 0.4rem;
  }
}
.cash-withdraw-amount {
  background-color: #ffffff;
  height: 2.56rem;
  padding-left: 0.38rem;
  padding-right: 0.44rem;
  & > p {
    padding-top: 0.26rem;
    color: #000;
  }
  .input-amount {
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #d9d9d9;
    justify-content: center;
    align-items: center;
    height: 1.3rem;
    span {
      font-size: 0.6rem;
      font-weight: 600;
    }
    input {
      border: none;
      margin-bottom: 0;
    }
  }
  .available-balance {
    height: 0.66rem;
    line-height: 0.66rem;
    color: #bebebe;
    // padding: 0.1rem 0.2rem;
  }
}
.prompt-message {
  margin-top: 0.4rem;
  padding-left: 0.38rem;
  color: #989898;
  line-height: 0.5rem;
}
footer {
  button {
    margin: auto;
    display: block;
    width: 80%;
    outline: none;
  }
}
</style>



