<template>
  <article class="income-detail">
    <back-head>
      <span class="back-content">收益详情</span>
    </back-head>
    <section class="income-detail-content">
      <ul>
        <li>
          <p>收益金额</p>
          <span>500.00</span>
        </li>
        <li>
          <p>类别</p>
          <span>业务分成</span>
        </li>
        <li>
          <p>业务来源</p>
          <span>机票订单</span>
        </li>
        <li>
          <p>收益时间</p>
          <span>2018-09-09 90:32</span>
        </li>
        <li>
          <p>分享账号</p>
          <span>2392823788</span>
        </li>
      </ul>
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  components: {
    BackHead
  },
  methods: {
    // 加载数据
  },
  created () {
    // 调用获取数据方法
  }
}
</script>
<style lang="scss" scoped>
@import '~styles/common.scss';
.income-detail{
    background: #f7f7f7;
    height: 100vh;
    overflow: hidden;
  .income-detail-content{
    ul{
      background: #ffffff;
      margin-top: 0.3rem;
      li{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        border-bottom: 1px solid #e3e3e3;
        height: 0.9rem;
        box-sizing: border-box;
        align-items: center;
        padding: 0 0.4rem 0 0.35rem;
        p,span{
          font-size: 0.32rem;
        }
        p{
          color: #626262;
        }
        span{
          color:#343434;
        }
      }
      li:last-child{
        border: none;
      }
    }
  }
}
</style>



