<template>
  <div class="foot-wrapper">
    <div class="foot-content" v-text="configData.msg" @click="goNext" :class="{'footIsActive':configData.isActive}"></div>
  </div>
</template>
<script>
export default {
  props: ['config'],
  data () {
    return {
      configData: this.config
    }
  },
  methods: {
    goNext () {
      if (this.configData.isActive) {
        this.$router.push({ name: this.configData.nextComponentName })
      } else {
        return false
      }
    }
  }
}
</script>
<style lang='scss' scoped>
@import '~styles/common.scss';
.foot-content {
  box-sizing: border-box;
  height: 0.9rem;
  border-top: 1px solid #d7d7d7;
  line-height: 0.9rem;
  text-align: center;
}
.footIsActive {
  background-color: #b40dba;
  color: #ffffff;
}
</style>



