<template>
  <article class="real-name-authentication">
    <back-head>
      <span class="back-content">实名认证</span>
    </back-head>
    <section class="real-name-authentication-content">
      <div class="real-name-authentication-content-head">
        <img src="" alt="">
        <div class="head-info">
          <p>王昭君</p>
          <span>2938********238</span>
        </div>
        <img src="../../../../../assets/images/icon/tubiao_75.png" alt="">
      </div>
      <div class="real-name-authentication-content-middle">
        <div class="person-information">
          <img src="../../../../../assets/images/icon/tubiao_09.png" alt="">
          <p>个人信息</p>
          <span>已完善</span>
          <span class="right-arrows" @click="$router.push({name:'PersonInformation'})"></span>
        </div>
        <div class="id-photo">
          <img src="../../../../../assets/images/icon/tubiao_14.png" alt="">
          <p>证件照片</p>
          <span>已上传</span>
        </div>
        <div class="phone-number">
          <img src="../../../../../assets/images/icon/tubiao_18.png" alt="">
          <p>手机号码</p>
          <span>13723937829</span>
        </div>
        <div class="bank-card">
          <img src="../../../../../assets/images/icon/tubiao_11.png" alt="">
          <p>银行卡</p>
          <span>454*********231</span>
        </div>
      </div>
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  data () {
    return {}
  },
  components: {
    BackHead
  }
}
</script>
<style lang='scss' scoped>
@import '~styles/common.scss';
.real-name-authentication-content {
  display: flex;
  flex-direction: column;
  .real-name-authentication-content-head {
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: #fff;
    height: 1.46rem;
    margin-top: 0.3rem;
    padding-left: 0.25rem;
    padding-right: 0.28rem;
    img:nth-of-type(1) {
      width: 1rem;
      height: 1rem;
      background-color: #888282;
      border-radius: 5px;
    }
    img:nth-of-type(2){
      width: 0.96rem;
      height: 0.35rem;
    }
    .head-info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      flex: 1;
      padding-left: 0.3rem;
      & > span {
        font-size: 0.24rem;
        color: #000;
      }
    }
    & > span {
      align-self: flex-start;
      color: red;
    }
  }
}
.real-name-authentication-content-middle {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  padding-left: 0.25rem;
  margin-top: 0.28rem;
  height: 3.35rem;
  div {
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    img{
      margin-right: 0.17rem;
    }
    p {
      flex: 1;
    }
    span {
      color:#939393;
    }
  }
  div:nth-child(1){
    img{
      width: 0.42rem;
      height: 0.35rem;
    }
  }
  div:nth-child(2){
    img{
      width: 0.42rem;
      height: 0.35rem;
    }
  }
  div:nth-child(3){
    img{
      width: 0.42rem;
      height: 0.44rem;
      padding: 0 0.05rem;
    }
  }
  div:nth-child(4){
    img{
      width: 0.42rem;
      height: 0.33rem;
    }
  }
  div:not(:first-of-type){
    span{
      margin-right: 0.25rem;
    }
  }
  div:not(:last-of-type){
    border-bottom: 1px solid #e4e4e4;
  }
}
.right-arrows {
  width: 0.18rem;
  height: 0.3rem;
  background-image: url('../../../../../assets/images/icon/tubiao_89.png');
  background-size: contain;
  background-repeat: no-repeat;
  margin-right: 0.25rem;
  margin-left: 0.28rem;
}
</style>



