// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
// import axios from 'axios'
import * as api from './axios/api.js'
import mui from '../static/js/mui.js'
import '../static/js/mui.picker.js'
import '../static/js/mui.dtpicker.js'
import md5 from 'js-md5'
import store from './store/index.js'
// 引入自定义插件
import Alert from './myPlugins/toast/index'
import Foot from './myPlugins/foot/index'
import Keyboard from './myPlugins/keyboard/index'
import Es6Promise from 'es6-promise'
Es6Promise.polyfill()
Vue.config.productionTip = false
Vue.prototype.$mui = mui
Vue.prototype.$api = api
Vue.prototype.$md5 = md5
// 引入自定义插件
Vue.use(Alert)
Vue.use(Foot)
Vue.use(Keyboard)
var wx = require('weixin-js-sdk')
Vue.prototype.$wx = wx
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: {
    App
  },
  template: '<App/>'
})
api.isLogin().then(res => { // 第一次进入或者刷新，请求查看登录状态，修改 isLogin 的值
  store.commit('changeLoginState', res.data.errorCode)
})
store.commit('changeMemberState', localStorage.getItem('member') === true ? true : Boolean(false)) // 刷新或进入的时候获取是否为会员的状态
// store.commit('saveMyReferrer', )
router.beforeEach((to, from, next) => {
  let login = store.state.isLogin
  let path = to.path
  console.log('lo', login)
  if (path === '/login' && !login) {
    next()
    return
  }
  if (login) {
    if (path === '/login') { // 如果是已经登录，并且跳转的路径是登录页的时候(刚登录，然后点后退，会出现该情况)，则跳转为首页
      next('/')
    } else {
      next()
    }
  } else if (path === '/') {
    next()
  } else {
    next({path: '/login'})
  }
});
(function (doc, win) {
  var docEl = doc.documentElement
  var resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize'
  var recalc = function () {
    var clientWidth = docEl.clientWidth
    if (!clientWidth) return
    if (clientWidth >= 640) {
      docEl.style.fontSize = '100px'
    } else {
      docEl.style.fontSize = 100 * (clientWidth / 750) + 'px'
    }
  }
  if (!doc.addEventListener) return
  win.addEventListener(resizeEvt, recalc, false)
  doc.addEventListener('DOMContentLoaded', recalc, false)
})(document, window)



