<template>
  <div class="login">
    <!-- <router-link to='-1'>返回</router-link> -->
    <back-head>
      <span class="back-content">分享系统</span>
    </back-head>
    <article class="logo-wrapper">
      <section class="logo">
        <img src="../../assets/images/icon/tubiao_03.png" alt="">
      </section>
      <section class="content">
        <img src="../../assets/images/icon/tubiao_06.png" alt="">
      </section>
    </article>
    <article class="login-wrapper">
      <section class="user-info">
        <form class="input-group">
          <div class="input-wraper">
            <input @blur="getReferrer" class="input-content" type="text" placeholder="请输入云返票务账号" v-model="userId">
          </div>
          <div class="input-wraper">
            <input class="input-content" :type="inputType" placeholder="请输入密码" v-model="password">
          </div>
          <div class="input-wraper">
            <input class="input-content" type="text" placeholder="推荐人账号" v-model="referrer">
          </div>
        </form>
      </section>
      <section class="login-button">
        <button v-if="isActive" @click="sub" class="active"></button>
        <button v-else @click="sub" disabled></button>
      </section>
    </article>
  </div>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
import {mapState, mapMutations} from 'vuex'
export default {
  name: 'Login',
  components: {
    BackHead
  },
  data () {
    return {
      userId: '',
      password: '',
      referrer: '',
      inputType: 'password',
      isActive: false,
      isShowPassword: false
    }
  },
  methods: {
    ...mapMutations(['changeLoginState', 'changeMemberState']),
    sub () {
      this.$api.login({
        account: this.userId,
        password: this.password,
        referrer: '12'
      }).then(res => {
        console.log('in', res.data.errorCode)
        if (res.data.errorCode === 0) {
          this.changeLoginState(res.data.errorCode)
          this.changeMemberState(res.data.data.is_member === 1 ? true : Boolean(false)) // 把是否为会员的状态保存到 vuex中
          localStorage.setItem('member', res.data.data.is_member === 1 ? true : Boolean(false)) // 把是否为会员状态保存到本地中
          this.$router.push({path: '/sharingSystem'})
        }
      })
    },
    isOpenEyes () {
      this.isShowPassword = !this.isShowPassword
      this.inputType = this.isShowPassword ? 'text' : 'password'
    },
    getReferrer () {
      this.$api.getMyReferrer({
        member_account: this.userId
      }).then(res => {
        if (res.data.errorCode === 0) {
          if (res.data.data.referrer) { // 如果有值，则获取，没有则设置为 12
            this.referrer = res.data.data.referrer
          } else {
            this.referrer = 12
          }
        }
      })
    }
  },
  watch: {
    userId: function (val) {
      if (val.trim().length !== 0 && this.password.trim().length !== 0) {
        this.isActive = true
      } else {
        this.isActive = false
      }
    },
    password: function (val) {
      if (val.trim().length !== 0 && this.userId.trim().length !== 0) {
        this.isActive = true
      } else {
        this.isActive = false
      }
    }
  },
  computed: mapState(['isLogin'])
}
</script>
<style lang="scss" scoped>
@import '~styles/common.scss';
.login{
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #f7f7f7;
}
.logo-wrapper{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 3.32rem;
  .logo{
    img{
      width: 1.83rem;
    }
    margin-right: 0.22rem;
  }
  .content{
    img{
      width: 3.11rem;
    }
  }
}
.login-wrapper {
  flex: 1;
  .user-info {
    .input-group {
      .input-wraper {
        display: flex;
        justify-content: center;
        height: 1.06rem;
        .input-content {
          height: 1.06rem;
          line-height: 53px;
          width: 6.83rem;
          box-sizing: border-box;
          border: none;
          border-bottom: 1px solid #cfcfcf;
          outline: none;
          padding-left: 0.2rem;
          padding-bottom: 0px;
          margin-bottom: 0;
          font-size: 0.32rem;
          background-color: #f7f7f7;
        }
        .input-content::-webkit-input-placeholder {
          color: #c6c6c6;
          font-size: 0.32rem;
          padding-bottom: 0px;
        }
      }
    }
  }
  .login-button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 0.8rem;
    button {
      width: 6.8rem;
      height: 0.9rem;
      outline: none;
      border: none;
      border-radius: 45px;
      margin: 0;
      padding: 0;
      font-size: 0.36rem;
      background-image: url('../../assets/images/icon/tubiao_04.png');
      background-size: 6.8rem 0.9rem;
    }
    .active {
      background-image: url('../../assets/images/icon/tubiao_05.png')
    }
  }
}

</style>



