<template>
  <article class="my-references">
    <back-head>
      <span class="back-content">我的推荐人</span>
    </back-head>
    <section class="my-references-content">
      <div class="references">
        <img src="../../../../assets/images/icon/tubiao_62.png" alt="">
        <p>我的推荐人</p>
        <span>12</span>
      </div>
      <div class="correlation-time">
        <img src="../../../../assets/images/icon/tubiao_80.png" alt="">
        <p>关联时间</p>
        <span>2018-08-24 12：07</span>
      </div>
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  data () {
    return {

    }
  },
  components: {
    BackHead
  }
}
</script>
<style lang='scss' scoped>
@import '~styles/common.scss';
.my-references-content{
  display: flex;
  flex-direction: column;
  margin-top: 0.3rem;
  background-color: #fff;
  height: 1.7rem;
  padding-left: 0.38rem;
  &>div{
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-right: 0.38rem;
    img:nth-of-type(1){
      height: 0.41rem;
      width: 0.37rem;
      margin-right: 0.24rem;
    }
    img:nth-of-type(2){
      height: 0.36rem;
      width: 0.51rem;
      margin-right: 0.15rem;
    }
    p{
      flex:1;
    }
    span{
      color: #999;
    }
  }
  div:nth-of-type(1){
    border-bottom: 1px solid #e4e4e4;
  }
}
</style>



