<template>
  <article class="set-password-confirm">
    <back-head>
      <span class="back-content">密码设置确认页</span>
    </back-head>
    <section class="set-password-confirm-content">
      <div class="tips">
        <p>请再次输入</p>
      </div>
      <div class="set-password-confirm-content-password-wrapper" ref="codeWrapper">
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
      </div>
      <p>支付密码不能是重复，连续的数字</p>
    </section>
    <footer>
      <foot-next :is-active="canNext" @click.native="verifyPassword"></foot-next>
    </footer>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
import FootNext from 'commonComponent/footNext/FootNext'
export default {
  components: {
    BackHead,
    FootNext
  },
  data () {
    return {
      verificationCode: [],
      canNext: false,
      pd: ''
    }
  },
  methods: {
    watchInp (event) {
      var e = event || window.event
      var target = e.target || e.srcElement
      var inps = this.$refs.codeWrapper.children
      var isNumber = /[\d]/.test(target.value)
      if (isNumber) {
        for (var i = 0; i < inps.length; i++) {
          if (target === inps[i]) {
            if (this.verificationCode['num' + i] === 'done') {
              target.value = target.value[1]
            }
            this.$set(this.verificationCode, i, target.value)
            this.verificationCode['num' + i] = 'done'
            if (
              i < inps.length - 1 &&
              this.verificationCode['num' + (i + 1)] !== 'done'
            ) {
              // 当下一个输入框没有有效值时，获取焦点
              inps[i + 1].focus()
              this.verificationCode['num' + (i + 1)] = 'will'
            }
          }
        }
      } else {
        target.value = ''
        // event.preventDefault()
      }
    },
    deleteCode (event) {
      var e = event || window.event
      var target = e.target || e.srcElement
      var inps = this.$refs.codeWrapper.children
      for (var i = 0; i < inps.length; i++) {
        if (target === inps[i] && i >= 0) {
          // ['num'+i] 属性有四个值，表示当前输入的四个状态：
          // will：刚获得焦点，未输入值
          // empty: 获得焦点后，输入了值，然后被删除了值
          // clear: 失去焦点并且没有输入值
          // done: 输入框有有效值
          // 第一次点击删除键，删除内容，并给对应的['num'+i]赋值为empty
          // empty 表示原先已有内容，现在删除了，
          // 第二次点击删除键，则退回上一个输入框，并把['num'+i]赋值为'claer'
          // 当焦点自动跳转到当前输入框，此时输入框没有内容，点击删除键(第一次)
          this.$set(this.verificationCode, i, target.value)
          if (
            this.verificationCode['num' + i] === 'empty' ||
            this.verificationCode['num' + i] === 'will'
          ) {
            if (i !== 0) {
              inps[i - 1].focus()
            }
            this.verificationCode['num' + i] = 'cealr'
          } else {
            this.verificationCode['num' + i] = 'empty'
          }
        }
      }
    },
    msgToast () {
      this.$mui.toast('修改成功', { duration: 1000, type: 'div' })
    },
    verifyPassword () {
      if (!this.canNext) {
        return false
      }
      var confirmPassword = this.$md5(this.verificationCode.join(''))
      var firstPassword = sessionStorage.getItem('pd')
      if (confirmPassword === firstPassword) {
        this.$mui.toast('设置密码成功', {type: 'div'})
        document.getElementsByClassName('mui-toast-container')[0].className +=
          ' message-middle'
      } else {
        this.canNext = false
        this.$mui.toast('密码输入不一致', {type: 'div'})
        document.getElementsByClassName('mui-toast-container')[0].className +=
          ' message-middle'
      }
    },
    savePassword () {
      // 将第一次填写的密码加密后保存到 sessionStorage 中,防止刷新上一次密码数据消失
      // 如果 有传参，则是新进入,需要重新保存密码
      if (this.$route.params.password) {
        sessionStorage.setItem('pd', this.$md5(this.$route.params.password.join('')))
      }
    }
  },
  watch: {
    verificationCode: function (val) {
      var confirmPassword = val.join('')
      if (confirmPassword.length === 6) {
        this.canNext = true
      } else {
        this.canNext = false
      }
    }
  },
  created () {
    this.savePassword()
  }
}
</script>
<style lang="scss" scoped>
@import '~styles/common.scss';
.set-password-confirm{
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
  background-color: #f7f7f7;
}
.set-password-confirm-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  & > p {
    align-self: center;
    color: #9c9c9c;
  }
  .tips {
    margin-top: 0.7rem;
    p{
      font-size: 0.32rem;
      color: #000;
      text-align: center;
    }
  }
  .set-password-confirm-content-password-wrapper {
    width: 100%;
    padding: 0;
    display: flex;
    justify-content: center;
    margin-top: 0.45rem;
    margin-bottom: 0.45rem;
    & > input {
      box-sizing: border-box;
      width: 0.74rem;
      height: 0.74rem;
      border-radius: 0;
      text-align: center;
      border: 1px solid #b3b3b3;
      border-right: none;
      &:last-child {
        border-right: 1px solid #b3b3b3;
      }
    }
  }
  .next {
    align-self: center;
    margin-bottom: 0.2rem;
  }
}
</style>



