<template>
  <article class="real-name-write">
    <back-head>
      <span class="back-content">实名认证</span>
    </back-head>
    <section class="real-name-write-content">
      <div class="content-top">
        <form action="" class="input-group">
          <div class="input-row">
            <label for="real-name">真实姓名</label>
            <input maxlength="8" type="text" id="real-name" name="real-name" placeholder="请输入真实姓名">
          </div>
          <div class="input-row">
            <label for="id-number">身份证号码</label>
            <input maxlength="18" type="text" id="id-number" name="id-number" placeholder="请输入身份证号">
          </div>
          <div class="input-row">
            <label for="phone-number">手机号</label>
            <input maxlength="12" type="tel" id="phone-number" name="phone-numuber" placeholder="请输入手机号">
          </div>
          <div class="input-row">
            <label for="card-number">银行卡号</label>
            <input maxlength="24" type="tel" id="card-number" name="card-number" placeholder="请输入银行卡号">
          </div>
        </form>
        <div class="photo">
          <p>身份证拍照</p>
          <!-- <img src="../../../../assets/images/icon/tubiao_59.png" alt=""> -->
          <input ref="camera" type="file" accept="iamge/*" capture="camera" @change="upImg" hidden>
          <!-- <input ref="camera" type="file" accept="iamge/*"  @change="upImg" hidden> -->
          <div ref="face" class="photo-face" @click="photograph('face')"></div>
          <div ref="emblem" class="photo-emblem" @click="photograph('emblem')"></div>
        </div>
        <div class="content-bottom">
          <p>注 : </p>
          <p>一天只有三次认证机会，确保您填写的信息真实有效</p>
        </div>
      </div>
    </section>
    <foot :config='config'></foot>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  data () {
    return {
      imgWrapper: '',
      config: {
        msg: '提交',
        isActive: true,
        nextComponentName: 'VerificationSuccess',
        cardNumber: ''
      }
    }
  },
  components: {
    BackHead
  },
  methods: {
    photograph (refname) {
      this.$refs.camera.click()
      this.imgWrapper = this.$refs[refname]
    },
    upImg () {
      var that = this
      var file = this.$refs.camera.files[0]
      var fr = new FileReader()
      var img = new Image()
      var isImg = this.imgWrapper.children[0]
      fr.onload = function (e) {
        if (isImg) {
          // 如果存在图片，则新上传的覆盖原有的。
          isImg.src = e.target.result
          return
        }
        img.src = e.target.result
        img.style.width = '2.9rem'
        img.style.height = '1.8rem'
        that.imgWrapper.appendChild(img)
        that.$refs.camera.value = '' // 为了可以上传相同的照片可以触发 onchange 时间
      }
      fr.readAsDataURL(file)
    },
    checkCardNumber () {

    }
  },
  mounted () {}
}
</script>
<style lang='scss' scoped>
@import '~styles/common.scss';
.real-name-write {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #f7f7f7;
}
.real-name-write-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  .content-top {
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    .input-group {
      display: flex;
      flex-direction: column;
      width: 100%;
      background-color: #fff;
      margin-top: 0.3rem;
      .input-row {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        height: 0.9rem;
        label {
          font-size: 0.32rem;
          padding-left: 0.4rem;
        }
        input {
          width: 60%;
          height: 0.88rem;
          line-height: 0.88rem;
          text-align: right;
          margin: 0;
          padding: 0;
          border: none;
          padding-right: 0.38rem;
          &::-webkit-input-placeholder {
            font-size: 0.32rem;
            text-align: right;
          }
        }
      }
      .input-row:not(:last-of-type) {
        border-bottom: 1px solid #e3e3e3;
      }
    }
    .photo {
      display: flex;
      flex-wrap: wrap;
      margin-top: 0.45rem;
      padding-left: 0.4rem;
      p {
        width: 100%;
        margin-bottom: 0.25rem;
      }
      .photo-face,
      .photo-emblem {
        width: 3.29rem;
        height: 1.87rem;
        border: 1px solid #cccccc;
        border-radius: 5px;
        background-size: cover;
        background-repeat: no-repeat;
      }
      .photo-face{
        margin-right: 0.2rem;
        background-image: url('../../../../assets/images/icon/tubiao_59.png');
      }
      .photo-emblem{
        background-image: url('../../../../assets/images/icon/tubiao_63.png');
      }
    }
    .content-bottom{
      margin-top: 0.4rem;
      color: #9a9a9a;
      p{
        font-size: 0.28rem;
      }
    }
  }
}
</style>



