<template>
  <article class="keyboard">
    <slot></slot>
    <section class="input-wrapper">
      <span :class="{circle:passwordArr[0]}"></span>
      <span :class="{circle:passwordArr[1]}"></span>
      <span :class="{circle:passwordArr[2]}"></span>
      <span :class="{circle:passwordArr[3]}"></span>
      <span :class="{circle:passwordArr[4]}"></span>
      <span :class="{circle:passwordArr[5]}"></span>
    </section>
    <section class="key-board" ref="key" @click="keyDown($event)">
      <span>1</span>
      <span>2</span>
      <span>3</span>
      <span>4</span>
      <span>5</span>
      <span>6</span>
      <span>7</span>
      <span>8</span>
      <span>9</span>
      <span class="empty"></span>
      <span>0</span>
      <span class="del">x</span>
    </section>
  </article>
</template>
<script>
export default {
  data () {
    return {
      passwordArr: []
    }
  },
  methods: {
    keyDown (event) {
      var target = event.target
      var passwordLength = this.passwordArr.length
      if (target.nodeName.toLocaleLowerCase() === 'span' && !target.matches('.empty') && !target.matches('.del') && passwordLength < 6) {
        this.passwordArr.push(target.innerText)
        if (passwordLength >= 5) {
          // 如果密码到了6位，发送验证请求
          // 验证成功后：跳转到验证成功界面
          this.$router.push({name: 'PaymentVerificationResult'})
        }
        target.className = 'active'
        setTimeout(() => {
          target.className = ''
        }, 100)
      }
      if (target.nodeName.toLocaleLowerCase() === 'span' && target.matches('.del')) {
        this.passwordArr.pop()
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.keyboard{
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  .input-wrapper{
    display: flex;
    flex-direction: row;
    padding: 0 .3rem 1rem;
    &>span{
      flex: 1;
      height: 0.88rem;
      border: 1px solid #aaaaaa;
      border-right: none;
    }
    span:last-child{
      border-right: 1px solid #aaaaaa;
    }
  }
  .key-board{
    display: flex;
    flex-wrap: wrap;
    &>span{
      box-sizing: border-box;
      width: 2.5rem;
      height: 1.2rem;
      text-align: center;
      line-height: 1.2rem;
      color: #000;
      font-size: 0.32rem;
    }
    span:nth-child(-n+9){
      // background: red;
      border-bottom: 1px solid #eaebec;
    }
    span:nth-child(2),span:nth-child(5),span:nth-child(8){
      // background: red;
      border-left: 1px solid #eaebec;
      border-right: 1px solid #eaebec;
    }
    .empty,.del{
      background-color: #c0c3c7;
    }
  }
  .circle{
    position: relative;
    &::after{
      content: "";
      display:inline-block;
      height: 0.2rem;
      width: 0.2rem;
      background: #000;
      border-radius: 50%;
      position: absolute;
      left: calc(50% - 0.1rem);
      top: calc(50% - 0.1rem);
    }
  }
  .active{
    background-color: #aaaaaa;
  }
}
</style>



