<template>
  <article class="upgrade-membership-NRN">
    <back-head>
      <span class="back-content">升级票务会员</span>
      <div class="back-right-icon">
        <span class="mui-icon mui-icon-help" @click="showAlert"></span>
      </div>
    </back-head>
    <section class="upgrade-membership-NRN-content">
      <div class="content-top">
        <img src="../../../../assets/images/icon/tubiao_107.png" alt="">
        <p>您尚未实名认证</p>
        <p>请先进行实名认证喔</p>
      </div>
      <div class="content-bottom">
        <p>实名认证成功后需验证票务</p>
        <p>余额<span>1000</span>元获得消费推荐权</p>
      </div>
    </section>
    <Foot :config = 'config'></Foot>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  data () {
    return {
      config: {
        msg: '实名认证',
        isActive: true,
        nextComponentName: 'RealNameWrite'
      }
    }
  },
  components: {
    BackHead
  },
  methods: {
    showAlert () {
      this.$alert({
        title: '费用说明',
        // content: '实名认证成功后需要验证票务余额1000元获得消费推荐权'
        hasBtn: false,
        isUseExplain: true
      })
    }
  },
  mounted () {
    // this.autFocus()
  }
}
</script>
<style lang='scss' scoped>
@import '~styles/common.scss';
.upgrade-membership-NRN /deep/ .myHead{
 .back-right-icon {
    margin-right: 0.25rem;
  }
}
.upgrade-membership-NRN{
  height: 100%;
  display: flex;
  flex-direction: column;
}
.upgrade-membership-NRN-content {
  flex:1;
  display: flex;
  flex-direction: column;
  .content-top {
    display: flex;
    flex-direction: column;
    align-items: center;
    img{
      width:2.2rem;
      height: 2.02rem;
      margin-top: 1.96rem;
      margin-bottom: 0.3rem;
    }
    p{
      color: #fc3b00;
    }
  }
  .content-bottom {
    margin-top: 0.25rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    p {
      color: #999;
    }
    span {
      color: #fc3b00;
    }
  }
}
.right-arrows {
  width: 0.18rem;
  height: 0.3rem;
  background-image: url('../../../../assets/images/icon/tubiao_89.png');
  background-size: contain;
  background-repeat: no-repeat;
  margin-right: 0.28rem;
  margin-left: 0.16rem;
}
</style>



