<template>
  <article class="upgrade-membersip-RN">
    <back-head>
      <span class="back-content">升级票务会员</span>
    </back-head>
    <section class="upgrade-membersip-RN-content">
      <div class="content-top">
        <img src="../../../assets/images/icon/tubiao_142.png" alt="">
        <p>恭喜您，您已经实名认证成功</p>
        <div>
          <span>请使用支付密码验证票务余额,</span>
          <span>获得消费推荐权</span>
        </div>
      </div>
    </section>
    <foot :config='config' @click.native="showKeyboard"></foot>
    <keyboard :isShow='show' @changeShowState="changeState"></keyboard>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  data () {
    return {
      config: {
        msg: '去验证',
        isActive: true
        // nextComponentName: 'VerifyThePayment'
      },
      show: false,
      hasSetPassword: false // 是否设置支付密码
    }
  },
  components: {
    BackHead
  },
  methods: {
    showKeyboard () {
      // this.$keyboard(() => {
      //   return this.$route.path
      // })
      if (!this.hasSetPassword) {
        this.show = true
      } else {
        this.$alert({
          title: '尚未设置支付密码',
          content: '请返回APP首页,右下角"我的"右上角"齿轮" - "安全设置"处设置',
          hasBtn: false
        })
      }
    },
    changeState (state) {
      this.show = state
    }
  }
}
</script>
<style lang='scss' scoped>
@import '~styles/common.scss';
.upgrade-membersip-RN {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.upgrade-membersip-RN-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  .content-top {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1.95rem;
    img {
      width: 2.68rem;
      height: 2.48rem;
    }
    p {
      margin-top: 0.3rem;
      margin-bottom: 0.25rem;
      font-size: 0.36rem;
      color: #fd350a;
    }
    div {
      display: flex;
      flex-direction: column;
      font-size: 0.28rem;
      color: #999;
    }
  }
}
</style>



