<template>
  <transition name="fade">
    <div class="alert-wrapper mask" v-show="show">
      <div class="alert-content">
        <p class="alert-title" v-text="config.title">费用说明</p>
        <p class="alert-info" v-text="config.content" v-if="!config.isUseExplain">实名认证成功后需要验证票务余额1000元获得消费推荐权</p>
        <explain v-else></explain>
        <div class="alert-btn" v-show="config.hasBtn">确定</div>
        <span class="alert-close" @click="closeAlert"></span>
      </div>
    </div>
  </transition>
</template>
<script>
const explainComponent = {
  template: `<div class="content-text-wrapper">
          <div class="content-text">
            <p>升级收益</p>
            <p>上级有10%收益</p>
          </div>
          <div class="content-text">
            <p>消费收益</p>
            <p>机票0.2%收益、酒店1%、火车票0.1%</p>
          </div>
          <div class="content-text">
            <p>免费入住商城</p>
          </div>
        </div>`
}
export default {
  data () {
    return {
      show: false,
      config: null
    }
  },
  methods: {
    closeAlert () {
      this.show = false
    }
  },
  components: {
    explain: explainComponent
  }
}
</script>

<style>
.alert-wrapper {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 998;
}
.alert-wrapper .alert-content {
  background: #ffffff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -70%);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 5.95rem;
  min-height: 3.63rem;
  border-radius: 3vw;
  /* height: 4.25rem; */
}
.alert-wrapper .alert-content .alert-title {
  width: 100%;
  height: 0.85rem;
  line-height: 0.85rem;
  text-align: center;
  background-color: #dac7de;
  font-weight: 600;
  border-top-left-radius: 3vw;
  border-top-right-radius: 3vw;
}
.alert-wrapper .alert-content .alert-info {
  width: 78%;
  margin: 0.55rem 0;
}
.alert-wrapper .alert-content .alert-btn {
  width: 78%;
  background-color: #1dbdf0;
  height: 0.68rem;
  line-height: 0.68rem;
  text-align: center;
  border-radius: 5px;
  margin-bottom: 0.6rem;
}
.alert-wrapper .alert-content .alert-close {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0.9rem);
  color: #fff;
  display: inline-block;
  width: 0.6rem;
  height: 0.6rem;
  background-image: url('data:image/png;base64,');
  background-size: cover;
  background-repeat: no-repeat;
}
.mask {
  background-color: rgba(0, 0, 0, 0.7);
}
.fade-enter-active,
.fade-leave-active {
  transition: 0.3s ease-out;
}
.fade-enter {
  opacity: 0;
  transform: scale(1.2);
}
.fade-leave-active {
  opacity: 0;
}
/* // explain 组件样式 */
.content-text-wrapper{
  width: 100%;
}
.content-text-wrapper .content-text {
  position: relative;
  margin-top: 0.3rem;
  padding-left: 0.7rem;
}
.content-text-wrapper .content-text p {
  font-size: 0.28rem;
}
.content-text-wrapper .content-text p:nth-child(1)::before {
  content: '';
  display: inline-block;
  box-sizing: border-box;
  width: 0.3rem;
  height: 0.3rem;
  border: 1px solid #9c9c9c;
  border-radius: 50%;
  font-size: 0.24rem;
  line-height: 0.3rem;
  text-align: center;
  color: #9c9c9c;
  position: absolute;
  left: 0.3rem;
  top: 0.08rem;
}
.content-text-wrapper .content-text:nth-child(1) p:nth-child(1)::before{
  content: '1'
}
.content-text-wrapper .content-text:nth-child(2) p:nth-child(1)::before{
  content: '2'
}
.content-text-wrapper .content-text:nth-child(3) p:nth-child(1)::before{
  content: '3'
}
.content-text-wrapper .content-text:nth-child(3){
  margin-bottom: 0.7rem;
}
/* .content-text-wrapper {
  .content-text {
    position: relative;
    margin-top: 0.3rem;
    padding-left: 0.7rem;
    p:nth-child(1)::before {
      content: '';
      display: inline-block;
      box-sizing: border-box;
      width: 0.3rem;
      height: 0.3rem;
      border: 1px solid #bd1ec5;
      border-radius: 50%;
      font-size: 0.24rem;
      line-height: 0.3rem;
      text-align: center;
      color: #bd1ec5;
      position: absolute;
      left: 0.3rem;
      top: 0.08rem;
    }
    &:nth-child(1) > p:nth-child(1)::before {
      content: '1';
    }
    &:nth-child(2) > p:nth-child(1)::before {
      content: '2';
    }
    &:nth-child(3) > p:nth-child(1)::before {
      content: '3';
    }
  }
} */
</style>



