<template>
  <article class="rember">
    <back-head>
      <span class="back-content">提现密码</span>
    </back-head>
    <section class="rember-content">
      <p>输入提现密码，完成身份验证</p>
      <div class="rember-content-code-wrapper" ref="codeWrapper">
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)'/>
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)'/>
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)'/>
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)'/>
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)'/>
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)'/>
      </div>
      <router-link class="rember-content-change-way" :to="{name:'DontRember'}">换个方式重置密码</router-link>
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  components: {
    BackHead
  },
  data () {
    return {
      verificationCode: []
    }
  },
  methods: {
    watchInp (event) {
      var e = event || window.event
      var target = e.target || e.srcElement
      var inps = this.$refs.codeWrapper.children
      var isNumber = /[\d]/.test(target.value)
      if (isNumber) {
        for (var i = 0; i < inps.length; i++) {
          if (target === inps[i]) {
            if (this.verificationCode['num' + i] === 'done') {
              target.value = target.value[1]
            }
            this.$set(this.verificationCode, i, target.value)
            this.verificationCode['num' + i] = 'done'
            if (i < inps.length - 1 && this.verificationCode['num' + (i + 1)] !== 'done') { // 当下一个输入框没有有效值时，获取焦点
              inps[i + 1].focus()
              this.verificationCode['num' + (i + 1)] = 'will'
            }
          }
        }
      } else {
        target.value = ''
        // event.preventDefault()
      }
    },
    deleteCode (event) {
      var e = event || window.event
      var target = e.target || e.srcElement
      var inps = this.$refs.codeWrapper.children
      for (var i = 0; i < inps.length; i++) {
        if (target === inps[i] && i >= 1) {
          // ['num'+i] 属性有四个值，表示当前输入的四个状态：
          // will：刚获得焦点，未输入值
          // empty: 获得焦点后，输入了值，然后被删除了值
          // clear: 失去焦点并且没有输入值
          // done: 输入框有有效值
          // 第一次点击删除键，删除内容，并给对应的['num'+i]赋值为empty
          // empty 表示原先已有内容，现在删除了，
          // 第二次点击删除键，则退回上一个输入框，并把['num'+i]赋值为'claer'
          // 当焦点自动跳转到当前输入框，此时输入框没有内容，点击删除键(第一次)
          this.$set(this.verificationCode, i, target.value)
          if (this.verificationCode['num' + i] === 'empty' || this.verificationCode['num' + i] === 'will') {
            if (i !== 0) {
              inps[i - 1].focus()
            }
            this.verificationCode['num' + i] = 'cealr'
          } else {
            this.verificationCode['num' + i] = 'empty'
          }
        }
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~styles/common.scss';
.rember{
  height: 100vh;
  background: #f7f7f7;
  overflow: hidden;
}
.rember-content {
  display: flex;
  flex-direction: column;
  & > p {
    text-align: center;
    padding: 0.76rem 0 0.5rem 0;
    color: #000;
  }
  .rember-content-code-wrapper {
    width: 100%;
    padding: 0;
    display: flex;
    justify-content: center;
    & > input {
      box-sizing: border-box;
      width: 0.74rem;
      height: 0.74rem;
      border-radius: 0;
      text-align: center;
      border: 1px solid #b3b3b3;
      border-right: none;
      &:last-child{
        border-right: 1px solid #b3b3b3;
      }
    }
  }
  .rember-content-change-way {
    margin-top: 0.48rem;
    align-self: center;
    color: #169dff;
  }
}
</style>



