<template>
  <div class="income-list">
    <back-head>
      <span class="back-content">
        收益明细
        <span ref="detailMenu" class="arrows" :class="detailMenuName" @click="showDetailMenu"></span>
      </span>
      <div class="back-right-icon">
        <img src="../../../../assets/images/icon/tubiao_114.png" class="search" alt="" @click="$router.push({name: 'IncomeSearch'})">
      </div>
    </back-head>
    <article class="income-info-wrapper">
      <section class="income-info-head">
        <div class="time">
          <span>本月</span>
          <img src="../../../../assets/images/icon/tubiao_111.png" alt="" @click="showDtPicker">
        </div>
        <div class="info-head-content">
          <div class="content-income">
            <span>收益</span>
            <span>￥155.00</span>
          </div>
          <div class="content-commission">
            <span>提成</span>
            <span>￥100.00</span>
          </div>
        </div>
      </section>
      <section class="income-info-list">
        <ul>
          <li v-for="(item,index) in incomeListData" :key="index" @click="goDetailPage(item)">
            <img :src="item.imgUrl" alt="">
            <div class="content">
              <div>
                <span v-text="item.ID">4545账号</span>
                <span v-text="item.incomeType">[推广返拥]</span>
              </div>
              <div>
                <span v-text="item.money">600.00</span>
                <span v-text="item.accountingDate">2018-08-24</span>
              </div>
            </div>
          </li>
        </ul>
      </section>
    </article>
    <article class="detail-menu-wrapper" :class="detailMenuWrapperName" ref="detailMenuWrapper" @click="chooseItem($event)">
      <section class="menu-content">
        <div class="menu-item" v-for="item in menuList" :key="item.keys">
          <img :src="item.itemSrc" alt="">
          <div class="item-content">
            <span :class="item.isActive" v-text="item.content">全部</span>
          </div>
        </div>
      </section>
    </article>
  </div>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  components: {
    BackHead
  },
  data () {
    return {
      incomeListData: null,
      flag: true, // 请求开关，避免连续点击导致连续发送请求
      isShowDetailMenu: false, // 是否显示收益明细下拉菜单
      mask: this.$mui.createMask(this.keepMaskShow),
      menuList: [
        {
          // itemSrc: '/static/images/icon/tubiao_113.png',
          itemSrc: require('../../../../assets/images/icon/tubiao_113.png'),
          content: '全部',
          isActive: 'item-active',
          keys: 'all'
        },
        {
          // itemSrc: '/static/images/icon/tubiao_131.png',
          itemSrc: require('../../../../assets/images/icon/tubiao_131.png'),
          content: '推广',
          isActive: '',
          keys: 'generalize'
        },
        {
          // itemSrc: '/static/images/icon/tubiao_150.png',
          itemSrc: require('../../../../assets/images/icon/tubiao_150.png'),
          content: '分成',
          isActive: '',
          keys: 'devide'
        },
        {
          // itemSrc: '/static/images/icon/tubiao_132.png',
          itemSrc: require('../../../../assets/images/icon/tubiao_132.png'),
          content: '提成',
          isActive: '',
          keys: 'commission'
        }
      ]
    }
  },
  created () {
    this.loadIncomeListData()
  },
  computed: {
    detailMenuName () { // 控制箭头方向的样式属性
      return {
        'arrows-top': !this.isShowDetailMenu,
        'arrows-bottom': this.isShowDetailMenu
      }
    },
    detailMenuWrapperName () { // 下拉菜单是否显示样式
      return {
        'detail-menu-wrapper-active': this.isShowDetailMenu
      }
    }
  },
  methods: {
    loadIncomeListData (type) {
      // 获取收益列表数据
      this.$api.getIncomeListData().then(res => {
        this.incomeListData = null
        if (res.data) {
          switch (type) {
            case '推广':
              this.incomeListData = res.data.tui
              break
            case '分成':
              this.incomeListData = res.data.fen
              break
            case '提现':
              this.incomeListData = res.data.ti
              break
            default:
              this.incomeListData = res.data.allList
              break
          }
        } else {
          this.incomeListData = []
        }
      })
    },
    goDetailPage (item) {
      this.$router.push({name: 'IncomeDetail', params: item})
    },
    showDtPicker () {
      // console.log(mui)
      var dtPicker = new this.$mui.DtPicker({
        type: 'month'
      })
      dtPicker.show(function (e) {
        console.log(e)
      })
    },
    showDetailMenu () {
      this.isShowDetailMenu = !this.isShowDetailMenu
      if (this.isShowDetailMenu) {
        this.mask.show()
        document.getElementsByClassName('mui-backdrop')[0].style.top = '4.43rem'
      } else {
        this.mask.close()
      }
    },
    chooseItem (e) {
      var target = e.target
      // var argTyp = null // 用来保存要查询数据的参数
      if (target.className === 'item-content') {
        for (var pro in this.menuList) { // 选中的项，添加样式，未选中的去掉。
          if (this.menuList[pro].content === target.innerText) {
            // argTyp = '选中类型' // 给argTyp 赋值
            this.menuList[pro].isActive = 'item-active'
          } else {
            this.menuList[pro].isActive = ''
          }
        }
        // this.$api.getData() //获取数据
        this.isShowDetailMenu = !this.isShowDetailMenu
        this.mask.close()
      }
    },
    keepMaskShow () { // mask 出现时，为了点击 mask 不消失
      if (event.target.className !== 'item-content' && event.target.className.indexOf('arrows') === -1) {
        return false
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~styles/common.scss';
.income-list /deep/ .myHead{
  .back-content{
    .arrows{
      content: "";
      position: absolute;
      transform: translate(0.12rem,0);
      display: inline-block;
      width: 0;
      border-width: 0.105rem;
      border-style: solid;
    }
    .arrows-top{
      top: 0.2275rem;
      border-color: #ffffff transparent transparent transparent;
    }
    .arrows-bottom{
      top:0.1225rem;
      border-color: transparent transparent #ffffff transparent;
    }
  }
}
.income-list{
  position: relative;
  background-color: #f7f7f7;
}
.back-right-icon{
  .search {
    width: 0.4rem;
    height: 0.4rem;
    margin-right: 0.23rem;
  }
}
article.income-info-wrapper {
  display: flex;
  flex-direction: column;
}
.income-info-head {
  box-sizing: border-box;
  border-top: 1px solid #d5d5d8;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 2.55rem;
  margin-bottom: 0.3rem;
  padding: 0.26rem 0.48rem 0 0.48rem;
  background-color: #ffffff;
  .time{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    span{
      color: #656565;
    }
    img{
      width: 0.4rem;
      height: 0.4rem;
    }
  }
  .info-head-content{
    margin-bottom: 0.5rem;
    div{
      display: flex;
      flex-direction: row;
      justify-content: center;
      span{
        // flex:1;
      }
      span:nth-of-type(1){
        text-align: right;
        color: #989898;
        margin-right: 0.06rem;
      }
      span:nth-of-type(2){
        font-weight: 600;
        margin-left: 0.06rem;
      }
    }
  }
}
.income-info-list {
  ul {
    display: flex;
    flex-direction: column;
    li {
      display: flex;
      flex-direction: row;
      background-color: #ffffff;
      align-items: center;
      img{
        width: 0.85rem;
        height: 0.85rem;
        margin-left: 0.24rem;
      }
      .content{
        flex: 1;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        height: 1.42rem;
        border-bottom: 1px solid #e3e3e3;
        box-sizing: border-box;
        padding:0 0.3rem 0 0.2rem;
        &>div{
          display: flex;
          flex-direction: column;
          span{
            color: #000;
          }
          span:nth-child(2){
            color: #9c9c9c;
          }
        }
        div:nth-child(1){
          // background-color: red;
        }
        div:nth-child(2){
          span:nth-child(1){
            font-weight: 600;
            text-align: right;
          }
        }
      }
    }
  }
}
.menu-wrapper {
  .menu {
    width: 3.2rem;
    ul {
      // height: 1.84rem;
      li {
        box-sizing: content-box;
        height: 0.3rem;
        // text-align: center;
        // padding-left: 0.6rem;
        span {
          padding-left: 0.3rem;
        }
        a {
          line-height: 0.48rem;
          margin-left: 0.1rem;
        }
      }
    }
  }
}
.detail-menu-wrapper{
  position: absolute;
  top: 0.88rem;
  left: 0;
  width: 100vw;
  background: #ffffff;
  display: none;
  z-index: 999;
  .menu-content{
    border-top: 1px solid #e3e3d3;
    border-bottom: 1px solid #e3e3e3;
    height: 3.55rem;
    box-sizing: border-box;
    .menu-item{
      display: flex;
      flex-direction: row;
      height: 0.89rem;
      align-items: center;
      padding-left: 0.25rem;
      img{
        width: 0.52rem;
        height: 0.54rem;
        margin-right: 0.24rem;
      }
      .item-content{
        flex: 1;
        border-bottom: 1px solid #e3e3e3;
        line-height: 0.89rem;
        .item-active{
          position: relative;
          &::after{
            content: '';
            position: absolute;
            right: -5.3rem;
            top: 0;
            display: inline-block;
            width: 0.36rem;
            height: 0.36rem;
            background-image: url('/static/images/icon/tubiao_136.png');
            background-size: cover;
            background-repeat: no-repeat;
          }
        }
      }
    }
    & div:last-child{
      .item-content{
        border-bottom: none;
      }
    }
  }
}
.detail-menu-wrapper-active{
  display: block;
}
.mui-backdrop{
  top:0.88rem;
}
</style>



