<template>
  <article class="my-profile">
    <back-head>
      <span class="back-content">我的资料</span>
    </back-head>
    <section class="my-profile-content">
      <div class="my-profile-content-head">
        <img src="" alt="" @click="$router.push({name:'PersonData'})">
        <div class="head-info">
          <p>昵称昵称</p>
          <span>账号：38238923</span>
        </div>
      </div>
      <div class="my-profile-content-middle">
        <div class="real-name-authentication">
          <img src="../../../../assets/images/icon/tubiao_72.png" alt="">
          <p>实名认证</p>
          <img src="../../../../assets/images/icon/tubiao_75.png" alt="">
          <span class="right-arrows" @click="$router.push({name:'RealNameAuthentication'})"></span>
        </div>
        <div class="my-references">
          <img src="../../../../assets/images/icon/tubiao_83.png" alt="">
          <p>我的推荐人</p>
          <span class="referrer">无推荐人</span>
          <span class="right-arrows" @click="$router.push({name:'MyReferences'})"></span>
        </div>
      </div>
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
export default {
  data () {
    return {
      isActive: true // 是否开通
    }
  },
  components: {
    BackHead
  },
  mounted () {
    // console.log(this.$route.params)
    // this.isActive = this.$route.params.isActive
  }
}
</script>
<style lang='scss' scoped>
@import '~styles/common.scss';
.my-profile /deep/ .myHead {
  ul {
    background-image: url('../../../../assets/images/bg/bg2.jpg');
    background-size: cover;
    background-repeat: no-repeat;
  }
}
.my-profile-content {
  display: flex;
  flex-direction: column;
  .my-profile-content-head {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 3.08rem;
    background-image: url('../../../../assets/images/bg/bg2.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 0 -1.3rem;
    img {
      width: 1.2rem;
      height: 1.2rem;
      background-color: #888282;
      border: 5px solid rgba(255,255,255,.4);
      border-radius: 5px;
      margin-bottom: 5px;
    }
    .head-info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      color: #fff;
      font-size: 0.32rem;
      text-align: center;
      span {
        font-size: 0.32rem;
      }
    }
  }
}
.my-profile-content-middle {
  display: flex;
  flex-direction: column;
  background: #ffffff;
  height: 1.88rem;
  margin-top: 0.28rem;
  padding-left: 0.38rem;
  div {
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    &:nth-of-type(1){
      border-bottom: 1px solid #e4e4e4;
    }
    img{
      height: 0.35rem;
    }
    img:nth-of-type(1){
      width: 0.42rem;
      margin-right: 0.13rem;
    }
    img:nth-of-type(2){
      width: 0.96rem;
    }
    p {
      flex: 1;
      font-size:0.32rem;
    }
    .referrer {
      color: #999;
    }
  }
  .real-name-authentication {
    .arrows-right {
      padding-left: 0.2rem;
    }
  }
  .my-references {
    .arrows-right {
      padding-left: 0.2rem;
    }
  }
}
.right-arrows {
  width: 0.18rem;
  height: 0.3rem;
  background-image: url('../../../../assets/images/icon/tubiao_89.png');
  background-size: contain;
  background-repeat: no-repeat;
  margin-right: 0.26rem;
  margin-left: 0.16rem;
}
</style>



